import {
        DeviceInfo,
        ENUM_PLAY_STATE_UPDATE,
        ENUM_PUBLISH_STATE_UPDATE, ENUM_PUBLISH_STREAM_STATE,
        ENUM_STREAM_SUB_CMD,
        MediaStreamConstraints, PlayOption,
        sdkErrorList
} from "../common/zego.entity";
import {playErrorList, publishErrorList} from "../common/zego.extern";
import {LoggerWeb} from "./zego.logger.webrtc";
import {ZegoStreamCenterWeb} from "./zego.streamCenter.web";
import {ClientUtil} from "../common/client-util";
import {BaseCenter} from "../common/clientBase/index";

export  class ZegoClient extends BaseCenter {
        
        streamCenter:ZegoStreamCenterWeb;
        
        constructor () {
                const log = new LoggerWeb();
                const streamCenter = new ZegoStreamCenterWeb(log);
                super (log,streamCenter);
                
                window.addEventListener ('message', event => {
                        const {data: {type, streamId, canRequestAudioTrack}, origin} = event;
                        if (type === 'SS_DIALOG_SUCCESS') { //user chose a stream
                                this.screenStreamFrom (streamId, canRequestAudioTrack, ClientUtil.actionSuccessCallback ('screenShare', this.stateCenter.callbackList));
                        }
                });
        }
        
        static screenShotReady: boolean = false;
        
        getSocket (server: string) {
                return new WebSocket (server);
        }
        
       
        enableCamera (localVideo: HTMLElement, enable: boolean) {
                this.logger.debug ("zc.p.ec.0 call");
                
                if (typeof enable !== "boolean") {
                        this.logger.error ("zc.p.ec.0 argument is not bool");
                        return false;
                }
                
                return this.streamCenter.enableCamera (localVideo, enable);
        }
        
        enableMicrophone (localVideo: HTMLElement, enable: boolean) {
                this.logger.debug ("zc.p.em.0 call");
                
                if (typeof enable !== "boolean") {
                        this.logger.error ("zc.p.em.0 argument is not bool");
                        return false;
                }
                
                return this.streamCenter.enableMicrophone (localVideo, enable);
        }
        
        setLocalAudioOutput (localVideo: HTMLElement, audioOutput: string): boolean {
                this.logger.debug ("zc.p.psao.1 call");
                
                return this.streamCenter.setPublishStreamAudioOutput (localVideo, audioOutput);
        }
        
        setPlayAudioOutput (streamid: string, audioOutput: string): boolean {
                this.logger.debug ("zc.p.psao.1 call");
                
                return this.streamCenter.setPlayStreamAudioOutput (streamid, audioOutput);
        }
        
        setCustomSignalUrl (signalUrl: string) {
                this.logger.debug ("zc.p.scs.0 call: " + signalUrl);
                
                if (!signalUrl || signalUrl.length == 0) {
                        this.logger.error ("zc.p.scs.0 param error");
                        return false;
                }
                
                if (signalUrl.indexOf ("wss://") != 0) {
                        this.logger.error ("zc.p.scs.0 url is not correct");
                        return false;
                }
                
                this.stateCenter.customUrl = signalUrl;
        };
        
        setQualityMonitorCycle (timeInMs: number) {
                if (typeof timeInMs === "number" && timeInMs >= 1000) {
                        this.streamCenter.setQualityMonitorCycle (timeInMs);
                }
        }
        
        /*
        *    "zc.p.sps.0": "ZegoClient.startPlayingStream",  // 
        */
        startPlayingStream (streamid: string, remoteVideo: HTMLElement, audioOutput?: string,playOption?:PlayOption): boolean {
                this.logger.debug ("zc.p.sps.0 call");
                
                if (!streamid || streamid === "") {
                        this.logger.error ("zc.p.sps.0 param error");
                        return false;
                }
                
                if (!remoteVideo) {
                        this.logger.error ("zc.p.sps.0 don't have remoteVideo");
                        return false;
                }
                
                if (this.stateCenter.customUrl) {
                        if (!this.streamCenter.setPlayStateStart (streamid, remoteVideo, audioOutput,playOption)) {
                                this.logger.error ("zc.p.sps.0 cannot start play");
                                return false;
                        }
                        
                        return this.streamCenter.startPlayingStream (streamid, [this.stateCenter.customUrl]);
                }
                
                
                if (!this.stateCenter.isLogin ()) {
                        this.logger.error ("zc.p.sps.0 not login");
                        return false;
                }
                
                let found = false;
                for (let i = 0; i < this.stateCenter.streamList.length; i++) {
                        if (this.stateCenter.streamList[i].stream_id === streamid) {
                                // id
                                found = true;
                                break;
                        }
                }
                
                if (found == false) {
                        this.logger.info ("zc.p.sps.0 cannot find stream");
                        // return false;
                }
                
                if (!this.streamCenter.setPlayStateStart (streamid, remoteVideo, audioOutput,playOption)) {
                        this.logger.info ("zc.p.sps.0 cannot start play");
                        return false;
                }
                
                //send request
                let body = {
                        stream_id: streamid,
                        ptype: "pull",
                        signals: this.streamCenter.getAllInUseUrl ()
                };
                
                this.socketCenter.registerRouter ('webrtc_url', msg => {
                        this.handleFetchWebRtcUrlRsp (msg)
                });
                this.socketCenter.sendMessage ("webrtc_url", body, undefined, (err, seq) => {
                        if (err == sdkErrorList.SEND_MSG_TIMEOUT) {
                                this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.error, streamid, playErrorList.DISPATCH_TIMEOUT);
                        }
                        else {
                                this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.error, streamid, playErrorList.DISPATCH_ERROR);
                        }
                        
                        this.streamCenter.stopPlayingStream (streamid);
                });
                
                return true;
        };
        
        
        /*
        *    "zc.p.sps.0.1": "ZegoClient.stopPlayingStream",
        */
        stopPlayingStream (streamid: string) {
                
                this.logger.debug ("zc.p.sps.1.0 call");
                
                if (!streamid || streamid === "") {
                        this.logger.info ("zc.p.sps.1.0 param error");
                        return false;
                }
                
                this.streamCenter.stopPlayingStream (streamid);
                
                for (let seq in this.stateCenter.streamUrlMap) {
                        if (this.stateCenter.streamUrlMap[seq] === streamid) {
                                delete this.stateCenter.streamUrlMap[seq];
                                break;
                        }
                }
                
                this.logger.debug ("zc.p.sps.1.0 call success");
                return true;
        };
        
        
        /*
         *    "zc.p.sp.0": "ZegoClient.startPreview", 
         */
        startPreview (localVideo: HTMLElement, mediaStreamConstraints: MediaStreamConstraints, success: Function, error: Function): boolean {
                this.logger.debug ("zc.p.sp.0 call");
                
                if (!localVideo) {
                        this.logger.error ("zc.p.sp.0 no localVideo");
                        return false;
                }
                
                return this.streamCenter.startPreview (localVideo, mediaStreamConstraints, success, error);
        };
        
        /*
        *    "zc.p.sp.1": "ZegoClient.stopPreview",
        */
        stopPreview (localVideo: HTMLElement): boolean {
                
                this.logger.debug ("zc.p.sp.1 call");
                
                if (!localVideo) {
                        this.logger.info ("zc.p.sp.1 param error");
                        return false;
                }
                
                return this.streamCenter.stopPreview (localVideo);
        };
        
        
        /*
        *    "zc.p.sps.1": "ZegoClient.startPublishingStream",
        */
        startPublishingStream (streamid: string, localVideo: HTMLElement, extraInfo?: any) {
                
                this.logger.debug ("zc.p.sps.1 call");
                
                if (!streamid) {
                        this.logger.error ("zc.p.sps.1 param error");
                        return false;
                }
                
                if (this.stateCenter.customUrl && this.stateCenter.customUrl.length != 0) {
                        this.stateCenter.publishStreamList[streamid] = {
                                state: ENUM_PUBLISH_STREAM_STATE.tryPublish,
                                extra_info: extraInfo
                        };
                        
                        if (!this.streamCenter.setPublishStateStart (streamid, localVideo)) {
                                this.logger.info ("zc.p.sps.1 cannot start publish");
                                return false;
                        }
                        
                        return this.streamCenter.startPublishingStream (streamid, [this.stateCenter.customUrl]);
                }
                
                if (!this.stateCenter.isLogin ()) {
                        this.logger.error ("zc.p.sps.1 not login");
                        return false;
                }
                
                this.stateCenter.publishStreamList[streamid] = {
                        state: ENUM_PUBLISH_STREAM_STATE.tryPublish,
                        extra_info: extraInfo
                };
                
                if (!this.streamCenter.setPublishStateStart (streamid, localVideo)) {
                        this.logger.error ("zc.p.sps.1 cannot start publish");
                        return false;
                }
                
                this.logger.info ("zc.p.sps.1 start publish");
                
                const body = {
                        stream_id: streamid,
                        ptype: "push",
                        signals: this.streamCenter.getAllInUseUrl ()
                };
                
                this.socketCenter.registerRouter ('webrtc_url', msg => {
                        this.handleFetchWebRtcUrlRsp (msg)
                });
                this.socketCenter.sendMessage ("webrtc_url", body, undefined, (err, seq) => {
                        if (err == sdkErrorList.SEND_MSG_TIMEOUT) {
                                this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.error, streamid, publishErrorList.DISPATCH_TIMEOUT);
                        } else {
                                this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.error, streamid, publishErrorList.DISPATCH_ERROR);
                        }
                        
                        this.streamCenter.stopPublishingStream (streamid);
                });
                
                return true;
        };
        
        
        //
        stopPublishingStream (streamid: string): boolean {
                
                this.logger.debug ("zc.p.sps.1.1 call");
                
                if (!streamid) {
                        this.logger.info ("zc.p.sps.1.1 param error");
                        return false;
                }
                
                this.streamCenter.stopPublishingStream (streamid);
                
                if (this.stateCenter.publishStreamList[streamid]) {
                        if (this.stateCenter.publishStreamList[streamid].state >= ENUM_PUBLISH_STREAM_STATE.update_info) {
                                this.streamHandler.updateStreamInfo (streamid, ENUM_STREAM_SUB_CMD.liveEnd);
                        }
                        delete this.stateCenter.publishStreamList[streamid];
                }
                
                return true;
        };
        
        startScreenShotChrome (callBack: (suc: boolean, stream: MediaStream) => void) {
                
                if (!ZegoClient.screenShotReady) {
                        this.logger.error (`zc.b.ss Please install the extension:1. Go to chrome://extensions  2. Check: "Enable Developer mode   3. Click: "Load the unpacked extension... 4. Choose "extension" folder from the repository 5. Reload this page
                                      `);
                        return false;
                } else {
                        window.postMessage ({type: 'SS_UI_REQUEST', text: 'start'}, '*');
                        // listen for messages from the content-script
                        ClientUtil.registerCallback ('screenShare',
                                    {success: callBack}, this.stateCenter.callbackList);
                        
                        
                }
        }
        
        startScreenShotFirFox (mediaSource: 'screen' | 'application' | 'window', audio, callBack: (suc: boolean, stream: MediaStream) => void) {
                const config = {
                        video: {},
                        audio: audio
                };
                config.video['mediaSource'] = mediaSource;
                
                navigator.mediaDevices
                .getUserMedia (config)
                .then (stream => {
                        this.stateCenter.screenShotStream = stream;
                        callBack (true, stream);
                }).catch (err => {
                        this.logger.error ('zc.b.ssf ' + err);
                        callBack (false, null);
                });
                
        }
        
        stopScreenShot () {
                this.stateCenter.screenShotStream.getTracks ().forEach (track => {
                        track.stop ();
                });
                window.postMessage ({type: 'SS_UI_CANCEL', text: 'start'}, '*');
        }
        
        // web
        WebrtcOnPublishStateUpdateHandle (type: 0 | 1 | 2, streamid: string, error: { code: string, msg: string }) {
                if (this.stateCenter.publishStreamList[streamid].state == ENUM_PUBLISH_STREAM_STATE.publishing) {
                        this.onPublishStateUpdate (type, streamid, error);
                }
        };
        
        // web
        setCDNInfo (streamInfo: { urls_flv: string, urls_hls: string, urls_rtmp: string },
                    streamItem: { urls_flv: string, urls_m3u8: string, urls_rtmp: string }) {
                streamInfo.urls_flv = streamItem.urls_flv;
                streamInfo.urls_hls = streamItem.urls_m3u8;
                streamInfo.urls_rtmp = streamItem.urls_rtmp;
        };
        
        screenStreamFrom (streamId: string,canRequestAudioTrack:boolean,callBack:Function) {
                
                
                const config = {};
                config['audio'] = {
                        mandatory: {
                                chromeMediaSource: 'desktop',
                                chromeMediaSourceId: streamId
                        }
                };
                config['video'] = {
                        mandatory : {
                                chromeMediaSource: 'desktop',
                                chromeMediaSourceId: streamId,
                                maxWidth: window.screen.width,
                                maxHeight: window.screen.height
                        }
                };
                
                !canRequestAudioTrack && (config['audio'] = false);
                navigator.mediaDevices
                .getUserMedia (config)
                .then (stream => {
                        this.stateCenter.screenShotStream = stream;
                        callBack(true,stream);
                }).catch (err => {
                        this.logger.error ('zc.b.ssf ' + err);
                        callBack(false,null);
                });
        }
        
        static  isSupportWebrtc(){
                return ClientUtil.isSupportWebrtc();
        }
        
        static  isSupportH264(sucCall,errCall){
                  ClientUtil.isSupportH264(sucCall,errCall);
        }
        
        
        
        enumDevices(deviceInfoCallback, error){
                ZegoClient.enumDevices(deviceInfoCallback, error);
        }
        
        static enumDevices (deviceInfoCallback, error) {
                if (navigator.mediaDevices === undefined || navigator.mediaDevices.enumerateDevices === undefined) {
                        if (error) {
                                error ("browser don't support enumerate devices");
                        }
                        return;
                }
                
                navigator.mediaDevices.enumerateDevices ().then (deviceInfos => {
                        let microphone = [];
                        let speaker = [];
                        let camera = [];
                        
                        for (let i = 0; i < deviceInfos.length; i++) {
                                let deviceInfo = deviceInfos[i];
                                if (deviceInfo.kind === 'audioinput') {
                                        microphone.push ({
                                                label: deviceInfo.label,
                                                deviceId: deviceInfo.deviceId
                                        });
                                }
                                if (deviceInfo.kind === 'audiooutput') {
                                        speaker.push ({
                                                label: deviceInfo.label,
                                                deviceId: deviceInfo.deviceId
                                        });
                                }
                                if (deviceInfo.kind === 'videoinput') {
                                        camera.push ({
                                                label: deviceInfo.label,
                                                deviceId: deviceInfo.deviceId
                                        });
                                }
                        }
                        
                        if (deviceInfoCallback) {
                                deviceInfoCallback ({
                                        microphones: microphone,
                                        speakers: speaker,
                                        cameras: camera
                                });
                        }
                        
                }).catch (err => {
                        if (error) {
                                error (err);
                        }
                });
        };
        
        
}

// listen for messages from the content-script
window.addEventListener ('message', event => {
        const {data: {type, streamId}, origin} = event;
        
        // NOTE: you should discard foreign events
        if (origin !== window.location.origin) {
                console.warn (
                            'ScreenStream: you should discard foreign event from origin:',
                            origin
                );
                // return;
        }
        
        // content-script will send a 'SS_PING' msg if extension is installed
        if (type === 'SS_PING') {
                ZegoClient.screenShotReady = true;
        }
        
        
});
