import {
        ENUM_PUBLISH_STREAM_STATE,
        ENUM_STREAM_SUB_CMD, MessageInfo, StreamInfo,
        StreamQuality, UserInfo
} from "../zego.entity";
import {SocketCenter} from "./socketCenter";
import {StateCenter} from "./stateCenter";
import {RoomHandler} from "./roomHandler";
import {StreamHandler} from "./streamHandler";
import {HeartBeatHandler} from "./heartBeatHandler";
import {MessageHandler} from "./messageHandler";
import {LiveHandler} from "./liveHandler";
import {Logger} from "../zego.logger";
import {ZegoStreamCenter} from "../ZegoStreamCenter";
import {ZegoWebSocket} from "../../wechatMini/zego.webSocket";

export abstract class Common {
        
        logger: Logger;
        
        streamCenter: ZegoStreamCenter;
        
        socketCenter: SocketCenter;
        
        roomHandler: RoomHandler;
        
        heartBeatHandler:HeartBeatHandler;
        
        
        streamHandler: StreamHandler;
        
        messageHandler:MessageHandler;
        
        liveHandler:LiveHandler;
        
        stateCenter: StateCenter;
        
        
        
        
        
        abstract getSocket (server: string): ZegoWebSocket | WebSocket;
        
        
        // web
        abstract  setCDNInfo (streamInfo: { urls_flv: string, urls_hls: string, urls_rtmp: string },
                                       streamItem: { urls_flv: string, urls_m3u8: string, urls_rtmp: string });
        
        //
        abstract WebrtcOnPublishStateUpdateHandle (type: 0 | 1 | 2, streamid: string, error: { code: string, msg: string });
        
        
        abstract stopPlayingStream (streamID: string);
        
        abstract onPlayStateUpdate (type: 0 | 1 | 2, streamid: string, error: { code: string, msg: string });
        
        
        abstract onPublishStateUpdate (type: 0 | 1 | 2, streamid: string, error: { code: string, msg: string });
        
        abstract stopPublishingStream (streamId: string);
        
        
        abstract onPublishQualityUpdate (streamid: string, streamQuality: StreamQuality);
        
        
        abstract onRecvJoinLiveRequest (requestId: string, from_userid: string, from_username: string, roomid: string);
        
        abstract onRecvInviteJoinLiveRequest (requestId: string, from_userid: string, from_username: string, roomid: string);
        
        abstract onRecvEndJoinLiveCommand (requestId: string, from_userid: string, from_username: string, roomid: string);
        
        abstract  onRecvCustomCommand (from_userid: string, from_idname: string, custom_content: string);
        
        abstract onUserStateUpdate (roomId: string, userList: UserInfo[]);
        
        abstract onRecvBigRoomMessage (messageList: MessageInfo[], roomId: string);
        
        abstract onRecvReliableMessage (type: string, seq: number, data: String);
        
        abstract  onStreamUpdated (type: number, streamList: StreamInfo[]);
        
        abstract  onStreamExtraInfoUpdated (streamList: StreamInfo[]);
        
        
        abstract startPublishingStream (streamid, localVideo, extraInfo)
        
       
        abstract startPlayingStream (streamid: string, remoteVideo: HTMLElement, audioOutput?: string);
        
      
      
        //
         onPlayStateUpdateHandle (type: 0 | 1 | 2, streamid: string, error: { code: string, msg: string }) {
                if (type == 1) {
                        this.stopPlayingStream (streamid);
                }
                this.onPlayStateUpdate (type, streamid, error);
        };
        
        
        //type: { start: 0, stop: 1}
        onPublishStateUpdateHandle (type: 0 | 1 | 2, streamid: string, error: { code: string, msg: string }) {
                if (type == 0) {
                        //start publish
                        if (this.stateCenter.publishStreamList[streamid]) {
                                if (this.stateCenter.publishStreamList[streamid].state == ENUM_PUBLISH_STREAM_STATE.tryPublish) {
                                        
                                        this.stateCenter.publishStreamList[streamid].state = ENUM_PUBLISH_STREAM_STATE.update_info;
                                        
                                        this.streamHandler.updateStreamInfo (streamid, ENUM_STREAM_SUB_CMD.liveBegin, this.stateCenter.publishStreamList[streamid].extra_info, (err) => {
                                                if (this.stateCenter.publishStreamList[streamid] && this.stateCenter.publishStreamList[streamid].state == ENUM_PUBLISH_STREAM_STATE.update_info) {
                                                        this.stateCenter.publishStreamList[streamid].state = ENUM_PUBLISH_STREAM_STATE.stop;
                                                        this.onPublishStateUpdate (1, streamid, err);
                                                        this.streamCenter.stopPlayingStream (streamid);
                                                }
                                        });
                                        
                                } else {
                                        this.WebrtcOnPublishStateUpdateHandle (type, streamid, error);
                                }
                                //publishing
                                // else if (this.stateCenter.publishStreamList[streamid].state == ENUM_PUBLISH_STREAM_STATE.publishing) {
                                //     this.onPublishStateUpdate(type, streamid, error);
                                // }
                        }
                } else {
                        this.onPublishStateUpdate (type, streamid, error);
                        
                        if (type == 1) {
                                this.stopPublishingStream (streamid);
                        }
                }
                
        };
        
        
        //
        resetStreamCenter () {
                this.stateCenter.customUrl && (this.stateCenter.customUrl = null);
                this.streamCenter.reset ();
                if (!this.socketCenter.isDisConnect ()) {
                        //send stream delete info
                        for (let streamid in this.stateCenter.publishStreamList) {
                                if (this.stateCenter.publishStreamList[streamid].state == ENUM_PUBLISH_STREAM_STATE.publishing) {
                                        this.streamHandler.updateStreamInfo (streamid, ENUM_STREAM_SUB_CMD.liveEnd, this.stateCenter.publishStreamList[streamid].extra_info);
                                }
                        }
                }
                
        }
        
        /*
        *    "zb.cm.hfwur": "ZegoClient.base.Common.handleFetchWebRtcUrlRsp",
        */
        protected handleFetchWebRtcUrlRsp (msg: any) {
                let streamId = msg.body.stream_id;
                
                if (msg.body.ptype === "push") {
                        if (this.stateCenter.publishStreamList[streamId]) {
                                this.streamCenter.startPublishingStream (streamId, msg.body.urls);
                        } else {
                                this.logger.error ("cb.cm.hfwur no streamid to publish");
                        }
                } else if (msg.body.ptype == "pull") {
                        //check streamid exist
                        let found = false;
                        for (let i = 0; i < this.stateCenter.streamList.length; i++) {
                                if (this.stateCenter.streamList[i].stream_id === streamId) {
                                        // id
                                        found = true;
                                        break;
                                }
                        }
                        
                        if (found == false) {
                                this.logger.warn ("cb.cm.hfwur cannot find stream, continue to play");
                               // return;
                        }
                        this.streamCenter.startPlayingStream (streamId, msg.body.urls);
                }
        }
        
        
      
        
      
        
       
        
       
      
     
      
        
      
        
      
        
      
      
        
        
}
