import {ENUM_RESOLUTION_TYPE, MediaStreamConstraints} from "../common/zego.entity";
import {Logger} from "../common/zego.logger";

export class ZegoPreview {
        logger: Logger;
        
        localVideo = null;
        localStream: MediaStream = null;
        videoInfo = {};
        
        previewSuc = false;
        
        constructor (private log: Logger) {
                this.logger = log;
        }
        
        
       
        
        /*
         *    "zp.gmsc.2": "getMediaStreamConstraints"
         */
        
        getMediaStreamConstraints (mediaStreamConfig: MediaStreamConstraints) {
                
                let mediaStreamConstraints = {
                        audio: null,
                        video: null
                };
                mediaStreamConstraints['audio'] = false;
                mediaStreamConstraints['video'] = false;
                
                //audio
                if (mediaStreamConfig.audio) {
                        if (mediaStreamConfig.audioInput) {
                                mediaStreamConstraints.audio = {
                                        deviceId: {
                                                exact: mediaStreamConfig.audioInput
                                        }
                                };
                        }
                        else {
                                mediaStreamConstraints.audio = true;
                        }
                }
                
                //video
                if (mediaStreamConfig.video) {
                        let width = 640;
                        let height = 480;
                        let frameRate = 15;
                        let bitRate = 800;
                        
                        //videoQuality
                        //1 QVGA
                        if (mediaStreamConfig.videoQuality === 1) {
                                width = ENUM_RESOLUTION_TYPE.LOW.width;
                                height = ENUM_RESOLUTION_TYPE.LOW.height;
                                frameRate = ENUM_RESOLUTION_TYPE.LOW.frameRate;
                                bitRate = ENUM_RESOLUTION_TYPE.LOW.bitRate;
                        }
                        //2 VGA
                        else if (mediaStreamConfig.videoQuality === 2) {
                                width = ENUM_RESOLUTION_TYPE.MEDIUM.width;
                                height = ENUM_RESOLUTION_TYPE.MEDIUM.height;
                                frameRate = ENUM_RESOLUTION_TYPE.MEDIUM.frameRate;
                                bitRate = ENUM_RESOLUTION_TYPE.MEDIUM.bitRate;
                        }
                        //3 HD
                        else if (mediaStreamConfig.videoQuality === 3) {
                                width = ENUM_RESOLUTION_TYPE.HIGH.width;
                                height = ENUM_RESOLUTION_TYPE.HIGH.height;
                                frameRate = ENUM_RESOLUTION_TYPE.HIGH.frameRate;
                                bitRate = ENUM_RESOLUTION_TYPE.HIGH.bitRate;
                        }
                        //custom
                        else if (mediaStreamConfig.videoQuality === 4) {
                                width = mediaStreamConfig.width;
                                height = mediaStreamConfig.height;
                                frameRate = mediaStreamConfig.frameRate;
                                bitRate = mediaStreamConfig.bitRate||800;
                        }
                        else {
                                this.logger.info ("zp.gmsc.2 user default");
                        }
                        
                        //horizontal
                        if (mediaStreamConfig.horizontal === true) {
                                let temp = height;
                                height = width;
                                width = temp;
                        }
                        
                        mediaStreamConstraints.video = {
                                width: width,
                                height: height,
                                frameRate: frameRate,
                                bitRate: bitRate
                        };
                        
                        //facingMode
                        if (mediaStreamConfig.facingMode != undefined) {
                                mediaStreamConstraints.video.facingMode = mediaStreamConfig.facingMode;
                        }
                        else if (mediaStreamConfig.videoInput != undefined) {
                                mediaStreamConstraints.video.deviceId = {
                                        exact: mediaStreamConfig.videoInput
                                };
                        }
                        
                        this.logger.info ("zp.gmsc.2 width: " + width + " height: " + height + " rate: " + frameRate);
                }
                
                return mediaStreamConstraints;
        }
        
        /*
         *    "zp.sv.2": "startPreview"
         */
        
        
        startPreview (localVideo: HTMLElement, mediaStreamConfig: MediaStreamConstraints, successCallback: Function, errorCallback: Function) {
                this.logger.debug ("zp.sv.2 called");
                this.localVideo = localVideo;
                
                if (navigator.mediaDevices === undefined || navigator.mediaDevices.getUserMedia == undefined) {
                        if (errorCallback) {
                                errorCallback ("browser don't support");
                        }
                        return;
                }
                
                
                //external media stream
                if (mediaStreamConfig.externalMediaStream instanceof MediaStream) {
                        this.logger.debug ("zp.sv.2 use external media stream");
                        
                        this.localStream = mediaStreamConfig.externalMediaStream;
                        
                        this.videoInfo = {
                                width: mediaStreamConfig.width,
                                height: mediaStreamConfig.height,
                                frameRate: mediaStreamConfig.frameRate,
                                bitRate: mediaStreamConfig.bitRate
                        };
                        
                        if (successCallback) {
                                successCallback ();
                        }
                        
                        return;
                } else if (mediaStreamConfig.externalCapture) {
                        const result = this.captureStream (localVideo);
                        if (result) {
                                if (successCallback) {
                                        successCallback ();
                                }
                        } else {
                                if (errorCallback) {
                                        errorCallback ("browser don't support");
                                }
                        }
                        
                        return;
                }
                
                const mediaStreamConstraints = this.getMediaStreamConstraints (mediaStreamConfig);
                this.videoInfo = mediaStreamConstraints.video;
                
                
                navigator.getUserMedia (mediaStreamConstraints, stream => {
                        this.logger.info ("zp.sv.2 success");
                        
                        if (!this.localVideo) {
                                this.logger.info ("zp.sv.2 no localVideo");
                                if (errorCallback) {
                                        errorCallback ("no localVideo");
                                }
                                return;
                        }
                        
                        this.localVideo.srcObject = stream;
                        this.localStream = stream;
                        this.previewSuc = true;
                        if (successCallback) {
                                successCallback ();
                        }
                }, error => {
                        this.logger.info ("zp.sv.2 failed");
                        if (errorCallback) {
                                errorCallback (error.name);
                        }
                });
        };
        
        
        /*
         *    "zp.cs.2": "captureStream"
         */
        captureStream (localVideo: HTMLElement) {
                if (!localVideo) {
                        this.logger.info ("zp.cs.2 no local video");
                        return false;
                }
                
                if (localVideo['captureStream']) {
                        this.localStream = localVideo['captureStream']();
                        this.logger.debug ("zp.cs.2 captureStream");
                }
                else if (localVideo['mozCaptureStream']) {
                        this.localStream = localVideo['mozCaptureStream'] ();
                        this.logger.debug ("zp.cs.2 mozCaptureStream");
                }
                else {
                        this.logger.info ("zp.cs.2 don't support");
                        return false;
                }
                
                this.videoInfo = {
                        width: localVideo['videoWidth'],
                        height: localVideo['videoHeight'],
                        frameRate: 0,
                        bitRate: 0
                };
                this.logger.debug ("zp.cs.2 called success");
                return true;
        }
        
        /*
         *    "zp.sv.2.1": "stopPreview"
         */
        stopPreview () {
                this.logger.info ("zp.sv.2.1 called");
                
                if (!this.localStream) {
                        return;
                }
                
                this.localStream.getTracks ().forEach (track => {
                        track.stop ();
                });
                
                this.localStream = null;
                
                this.localVideo.srcObject = null;
                this.localVideo = null;
                
                this.videoInfo = {};
        };
        
        /*
         *    "zp.em.2": "enableMicrophone"
         */
        enableMicrophone = (enable: boolean) => {
                if (!this.localStream) {
                        this.logger.error ("zp.em.2 no localStream");
                        return false;
                }
                
                this.localStream.getAudioTracks ().forEach (
                            (track) => {
                                    track.enabled = enable;
                            }
                );
                
                this.logger.debug ("zp.em.2 call success");
                return true;
        };
        
        /*
         *    "zp.ec.2": "enableCamera"
         */
        enableCamera = (enable: boolean) => {
                if (!this.localStream) {
                        this.logger.error ("zp.ec.2 no localStream");
                        return false;
                }
                
                this.localStream.getVideoTracks ().forEach (
                            (track) => {
                                    track.enabled = enable;
                            }
                );
                
                this.logger.debug ("zp.ec.2 call success");
                return true;
        };
        
        /*
         *    "zp.sad.2": "setAudioDestination"
         */
        setAudioDestination = audioOutput => {
                if (!this.localVideo) {
                        this.logger.error ("zp.sad.2 no localVideo");
                        return false;
                }
                
                if (this.localVideo.sinkId !== 'undefined') {
                        this.localVideo.setSinkId (audioOutput).then (() => {
                                this.logger.info ("zp.sad.2 success device: " + audioOutput);
                                // _this.audioOutput = audioOutput;
                        }).catch (error => {
                                this.logger.info ("zp.sad.2 " + error.name);
                        });
                        return true;
                }
                else {
                        this.logger.error ("zp.sad.2 browser does not suppport");
                        return false;
                }
        }
        
        
}
