export class ListNode {
        _id: number = null;
        _data: any;
        next: ListNode = null;
        prev: ListNode = null;

        constructor(id: number = null, data: any = null) {
                this._id = id;
                this._data = data;
        }


        set id(id: number) {
                this._id = id;
        }

        get id() {
                return this._id;
        }

        set data(data: any) {
                this._data = data;
        }

        get data() {
                return this._data;
        }

        hasNext() {
                return this.next && this.next.id;
        }


        hasPrev() {
                return this.prev && this.prev.id;
        }

}

export class LinkedList {
        //initialize end buffer nodes
        start = new ListNode();
        end = new ListNode();

        //initialize counters
        _idCounter = 0;
        _numNodes = 0;

        constructor() {
                //initialize node pointers
                this.start.next = this.end;
                this.start.prev = null;
                this.end.prev = this.start;
                this.end.next = null;
        }

        /**
         *   Inserts a node before another node in the linked list
         *   @param {Node} toInsertBefore
         *   @param {Node} node
         */
        insertBefore(toInsertBefore: ListNode, data: any) {
                let newNode = new ListNode(this._idCounter, data);

                newNode.next = toInsertBefore;
                newNode.prev = toInsertBefore.prev;

                toInsertBefore.prev.next = newNode;
                toInsertBefore.prev = newNode;

                ++this._idCounter;
                ++this._numNodes;
                return newNode;
        }

        /**
         *   Adds data wrapped in a Node object to the end of the linked list
         *   @param {object} data
         */
        addLast(data: any) {
                return this.insertBefore(this.end, data);
        }

        /**
         *   Alias for addLast
         *   @param {object} data
         */
        add(data: any) {
                return this.addLast(data);
        }

        /**
         *   Gets and returns the first node in the linked list or null
         *   @return {Node/null}
         */
        getFirst() {
                if (this._numNodes === 0) {
                        return null;
                } else {
                        return this.start.next;
                }
        }

        /**
         *   Gets and returns the last node in the linked list or null
         *   @return {Node/null}
         */
        getLast() {
                if (this._numNodes === 0) {
                        return null;
                } else {
                        return this.end.prev;
                }
        }

        /**
         *   Gets and returns the size of the linked list
         *   @return {number}
         */
        size() {
                return this._numNodes;
        }

        /**
         *   (Internal) Gets and returns the node at the specified index starting from the first in the linked list
         *   Use getAt instead of this function
         *   @param {number} index
         */
        getFromFirst(index) {
                let count = 0, temp = this.start.next;
                if (index >= 0) {
                        while (count < index && temp !== null) {
                                temp = temp.next;
                                ++count;
                        }
                } else {
                        temp = null;
                }

                if (temp === null) {
                        throw 'Index out of bounds.';
                }

                return temp;
        }

        /**
         *   Gets and returns the Node at the specified index in the linked list
         *   @param {number} index
         */
        get(index) {
                let temp = null;

                if (index === 0) {
                        temp = this.getFirst();
                } else if (index === this._numNodes - 1) {
                        temp = this.getLast();
                } else {
                        temp = this.getFromFirst(index);
                }

                return temp;
        }

        /**
         *   Removes and returns node from the linked list by rearranging pointers
         *   @param {Node} node
         *   @return {Node}
         */
        remove(node) {
                node.prev.next = node.next;
                node.next.prev = node.prev;

                --this._numNodes;

                return node;
        }

        /**
         *   Removes and returns the first node in the linked list if it exists, otherwise returns null
         *   @return {Node/null}
         */
        removeFirst() {
                let temp = null;

                if (this._numNodes > 0) {
                        temp = this.remove(this.start.next);
                }

                return temp;
        }

        /**
         *   Removes and returns the last node in the linked list if it exists, otherwise returns null
         *   @return {Node/null}
         */
        removeLast() {
                let temp = null;

                if (this._numNodes > 0) {
                        temp = this.remove(this.end.prev);
                }

                return temp;
        }

        /**
         *   Removes all nodes from the list
         */
        removeAll() {
                this.start.next = this.end;
                this.end.prev = this.start;
                this._numNodes = 0;
                this._idCounter = 0;
        }

        /**
         *    Iterates the list calling the given fn for each node
         *    @param {function} fn
         */
        each(iterator) {
                let temp = this.start;

                while (temp.hasNext()) {
                        temp = temp.next;
                        iterator(temp);
                }
        }

        find(iterator) {
                let temp = this.start,
                        found = false,
                        result = null;

                while (temp.hasNext() && !found) {
                        temp = temp.next;
                        if (iterator(temp)) {
                                result = temp;
                                found = true;
                        }
                }

                return result;
        }

        map(iterator) {
                let temp = this.start,
                        results = [];

                while (temp.hasNext()) {
                        temp = temp.next;
                        if (iterator(temp)) {
                                results.push(temp);
                        }
                }

                return results;
        }

        /**
         *    Alias for addLast
         *    @param {object} data
         */
        push(data) {
                return this.addLast(data);
        }

        /**
         *    Performs insertBefore on the first node
         *    @param {object} data
         */
        unshift(data) {
                if (this._numNodes > 0) {
                        this.insertBefore(this.start.next, data);
                } else {
                        this.insertBefore(this.end, data);
                }
        }

        /**
         *    Alias for removeLast
         */
        pop() {
                return this.removeLast();
        }

        /**
         *    Alias for removeFirst()
         */
        shift() {
                return this.removeFirst();
        }
}


