import {ENUM_RUN_STATE, LinkedList} from "../zego.entity";
import {getSeq} from "../zego.extern";

export class StateCenter {
        
        testEnvironment = false;
        
        configOK = false;
        appid: number;
        userid: string;
        sessionid: string;
        roomid: string;
        token: string;
        role: 1 | 2;
        server: string;
        idName: string;
        nickName: string;
        userStateUpdate: boolean;
        roomCreateFlag: number = 1;
        runState: ENUM_RUN_STATE = ENUM_RUN_STATE.logout;
        lastRunState: ENUM_RUN_STATE = ENUM_RUN_STATE.logout;
        
        callbackList: { [index: string]: Function } = {};
        
        
        streamQuerying: boolean;
        streamSeq: number;
        streamList = [];
        publishStreamList = {};
        
        //
        userQuerying = false;
        userTempList = [];
        userSeq: number = 0;
        
        
        //command check timout
        sendCommandMap = {};
        sendCommandList = new LinkedList ();
        sendDataMap = {};
        sendDataList = new LinkedList ();
        
        joinLiveCallbackMap = {};
        joinLiveRequestMap = {};
        streamUrlMap = {};
        cmdCallback = {};
        
        customUrl: string;
        
        //x
        transSeqMap = {};
        realyMessageList = [];
        relayTimer = null;
        bigImLastTimeIndex = 0;
        bigIMmessageList = [];
        bigImCallbackMap = {};
        bigImTimer = null;
        
        serverTimeOffset = 0;
        datiTimeWindow = 0;
        bigimTimeWindow = 0;
        
        bigImMessageList = [];
        
        
        screenShotStream: MediaStream;
        
        
        tryLoginCount = 0;
        tryLoginTimer = null;
        
        
        tryHeartbeatCount: number;
        
        heartbeatInterval: number;
        heartbeatTimer = null;
        
        
        sendDataCheckTimer = null;
        
        sendDataCheckInterval = 2000; //
        
        sendDataTimeout = 5 * 1000; //
        sendDataDropTimeout = 10 * 1000; //
        sendDataCheckOnceCount = 100; //
        
        
        sendRoomMsgTime = 0; //
        SendRoomMsgInterval = 500; //500
        
        cmdSeq = 0;
        
        constructor () {
        
        }
        
        //
        isLogin () {
                return this.runState === ENUM_RUN_STATE.login;
        }
        
        //requestId
        getRequestId (): string {
                return this.idName + "-" + getSeq ();
        }
        
        getSignalCmdContent (requestId: string, dest_id_name: string, result?: number): string {
                var data = {
                        "request_id": requestId,
                        "room_id": this.roomid,
                        "from_userid": this.idName,
                        "from_username": this.nickName,
                        "to_userid": dest_id_name
                };
                
                if (result != undefined) {
                        data["result"] = result;
                }
                return JSON.stringify (data);
        }
}
