import {ENUM_REMOTE_TYPE, ENUM_LOG_LEVEL} from "./zego.entity";
import {ZegoWebSocket} from "../wechatMini/zego.webSocket";

export const D = ['00', '01', '02', '03', '04', '05', '06', '07', '08', '09'];

export abstract class Logger {
        
        appid: number;
        roomid: string;
        sessionid: string;
        userid: string;
        userName: string;
        version: string;
        logType: ENUM_REMOTE_TYPE;
        logLevel: ENUM_LOG_LEVEL;
        logRemoteLevel: ENUM_LOG_LEVEL;
        
        websocket: WebSocket | ZegoWebSocket;
        url: string;
        logUploadTimer = null;
        logUploadInterval = 1000 * 10;
        
        timeInterval: number;
        
        logCache = [];
        logCacheSend = [];
        logCacheMax = 100;
        
        abstract openWebSocketLogServer (url: string);
        
        abstract SendHttpsLog ();
        
        constructor () {
        }
        
        
        setLogLevel (logLevel: ENUM_LOG_LEVEL) {
                if (this.logLevel < ENUM_LOG_LEVEL.debug || this.logLevel > ENUM_LOG_LEVEL.report) {
                        this.logLevel = ENUM_LOG_LEVEL.disable;
                } else {
                        this.logLevel = logLevel;
                }
        }
        
        setRemoteLogLevel (logLevel: ENUM_LOG_LEVEL) {
                if (this.logRemoteLevel < ENUM_LOG_LEVEL.debug || this.logRemoteLevel > ENUM_LOG_LEVEL.report) {
                        this.logRemoteLevel = ENUM_LOG_LEVEL.disable;
                } else {
                        this.logRemoteLevel = logLevel;
                }
        }
        
        
        setSessionInfo (appid: number, roomid: string, sessionid: string, userid: string, userName: string, version: string) {
                this.appid = appid;
                this.roomid = roomid;
                this.sessionid = sessionid;
                this.userid = userid;
                this.userName = userName;
                this.version = version;
        };
        
        openLogServer (url: string) {
                if (url.startsWith ("wss:")) {
                        this.logType = ENUM_REMOTE_TYPE.websocket;
                        this.openWebSocketLogServer (url);
                } else if (url.startsWith ("https:")) {
                        this.logType = ENUM_REMOTE_TYPE.https;
                        this.openHttpsLogServer (url);
                } else {
                        this.logType = ENUM_REMOTE_TYPE.disable;
                }
        }
        
        stopLogServer () {
                if (this.logType == ENUM_REMOTE_TYPE.websocket) {
                        this.stopWebSocketServer ();
                }
                else if (this.logType == ENUM_REMOTE_TYPE.https) {
                        //send last data
                        this.SendHttpsLog ();
                        this.stopHttpsServer ();
                }
                
                this.logType = ENUM_REMOTE_TYPE.disable;
        };
        
        
        stopWebSocketServer () {
                if (this.websocket) {
                        this.websocket.onclose = null;
                        this.websocket.onerror = null;
                        this.websocket.close ();
                        this.websocket = null;
                }
        }
        
        openHttpsLogServer (url: string) {
                this.url = url;
                if (!url) {
                        return;
                }
                
                this.stopHttpsServer ();
                
                //start timer
                if (!this.logUploadTimer) {
                        this.logUploadTimer = setInterval (() => {
                                this.SendHttpsLog ();
                        }, this.logUploadInterval);
                }
                
        }
        
        stopHttpsServer () {
                //stop timer
                if (this.logUploadTimer) {
                        clearInterval (this.logUploadTimer);
                        this.logUploadTimer = null;
                }
        }
        
        
        report (dataItem: any) {
                const log = this.logReportParamList (ENUM_LOG_LEVEL.report, dataItem);
                if (this.logLevel !== ENUM_LOG_LEVEL.disable && this.logLevel <= ENUM_LOG_LEVEL.report) {
                        console.debug.apply (console, log);
                }
                
                
                //report 
                this.RemoteLog (ENUM_LOG_LEVEL.report, log, true);
        }
        
        debug (...values) {
                let log = this.logParamList (ENUM_LOG_LEVEL.debug, values.join (''));
                if (this.logLevel !== ENUM_LOG_LEVEL.disable && this.logLevel <= ENUM_LOG_LEVEL.debug) {
                        console.debug.apply (console, log);
                }
                
                this.log (ENUM_LOG_LEVEL.debug, log);
        }
        
        info (...values) {
                let log = this.logParamList (ENUM_LOG_LEVEL.info, values.join (''));
                if (this.logLevel !== ENUM_LOG_LEVEL.disable && this.logLevel <= ENUM_LOG_LEVEL.info) {
                        console.info.apply (console, log);
                }
                
                this.log (ENUM_LOG_LEVEL.info, log);
        }
        
        warn (...values) {
                let log = this.logParamList (ENUM_LOG_LEVEL.warn, values.join (''));
                if (this.logLevel !== ENUM_LOG_LEVEL.disable && this.logLevel <= ENUM_LOG_LEVEL.warn) {
                        console.warn.apply (console, log);
                }
                
                this.log (ENUM_LOG_LEVEL.warn, log);
        }
        
        error (...values) {
                let log = this.logParamList (ENUM_LOG_LEVEL.error, values.join (''));
                if (this.logLevel !== ENUM_LOG_LEVEL.disable && this.logLevel <= ENUM_LOG_LEVEL.error) {
                        console.error.apply (console, log);
                }
                
                this.log (ENUM_LOG_LEVEL.error, log);
        }
        
        log (level: ENUM_LOG_LEVEL, log: any) {
                
                if (this.logLevel !== ENUM_LOG_LEVEL.disable && this.logLevel <= level) {
                        this.logCache.push (log);
                        while (this.logCache.length > this.logCacheMax) {
                                this.logCache.shift ();
                        }
                }
                
                if (this.logRemoteLevel !== ENUM_LOG_LEVEL.disable && this.logRemoteLevel <= level) {
                        this.RemoteLog (level, log);
                }
        };
        
        RemoteLog (level: ENUM_LOG_LEVEL, log: any, force = false) {
                
                if (this.url == "") {
                        return;
                }
                
                if (this.logType == ENUM_REMOTE_TYPE.websocket) {
                        this.RemoteWebSocketLog (level, log);
                }
                else if (this.logType == ENUM_REMOTE_TYPE.https) {
                        this.RemoteHttpsLog (level, log, force);
                }
        };
        
        RemoteWebSocketLog (level: ENUM_LOG_LEVEL, log: any) {
                if (this.websocket == null || this.websocket.readyState == 2 || this.websocket.readyState == 3) {
                        let url = this.url;
                        this.url = "";
                        this.openLogServer (url);
                        if (this.logCacheSend.length < this.logCacheMax) {
                                this.logCacheSend.push (log);
                        }
                }
                else if (this.websocket.readyState == 0) {
                        if (this.logCacheSend.length < this.logCacheMax) {
                                this.logCacheSend.push (log);
                        }
                }
                else if (this.websocket.readyState == 1) {
                        if (this.logCacheSend.length > 0) {
                                let logBefore = "";
                                for (let i = 0; i < this.logCacheSend.length; i++) {
                                        logBefore = logBefore + this.logCacheSend[i] + "\n";
                                }
                                log = logBefore + log;
                                this.logCacheSend = [];
                        }
                        this.websocket.send(log);
                }
                else {
                        console.warn ("wrong socket state:" + this.websocket.readyState);
                        if (this.logCacheSend.length < this.logCacheMax) {
                                this.logCacheSend.push (log);
                        }
                }
        }
        
        RemoteHttpsLog (level: ENUM_LOG_LEVEL, log: any, force: boolean) {
                this.logCacheSend.push (log);
                if (this.logCacheSend.length >= this.logCacheMax || force === true) {
                        this.SendHttpsLog ();
                }
        }
        
        logParamList (level: ENUM_LOG_LEVEL, logInfo: string) {
                
                let t = new Date ();
                let stringTime = (t.getFullYear ()) + "/";
                stringTime += (D[t.getMonth () + 1] || t.getMonth () + 1) + "/";
                stringTime += (D[t.getDate ()] || t.getDate ()) + " ";
                stringTime += (D[t.getHours ()] || t.getHours ()) + ":";
                stringTime += (D[t.getMinutes ()] || t.getMinutes ()) + ":";
                stringTime += (D[t.getSeconds ()] || t.getSeconds ());
                stringTime += "." + t.getTime () % 1000;
                
                //get first space from logInfo
                let action = logInfo.substr (0, logInfo.indexOf (' '));
                if (action.length == 0) {
                        action = logInfo;
                }
                
                let content = logInfo.substr (logInfo.indexOf (' ') + 1);
                if (content.length == 0) {
                        content = "";
                }
                
                let s = {
                        "time": stringTime,
                        "level": level,
                        "action": action,
                        "content": content,
                        "appid": this.appid,
                        "roomid": this.roomid,
                        "userid": this.userid,
                        "userName": this.userName,
                        "sessionid": this.sessionid
                };
                
                return [JSON.stringify (s)];
        }
        
        abstract logReportParamList (level: ENUM_LOG_LEVEL, logInfo: string);
}



