import {ZegoDataReport} from "../common/zego.datareport";
import {ZegoPreview} from "./zego.preview";
import {ZegoPublish} from "./zego.publish";
import {
        ENUM_PLAY_STATE_UPDATE,
        ENUM_PUBLISH_STATE_UPDATE,
        playErrorList,
        publishErrorList
} from "../common/zego.extern";
import {ENUM_SIGNAL_STATE, MediaStreamConstraints, PlayerInfo, PlayOption, SignalInfo} from "../common/zego.entity";
import {ZegoSignal} from "../common/zego.signal";
import {ZegoPlayWeb} from "./zego.play.web";
import {ZegoStreamCenter} from "../common/ZegoStreamCenter";
import {LoggerWeb} from "./zego.logger.webrtc";

export class ZegoStreamCenterWeb extends ZegoStreamCenter {
        playerList: { [index: string]: PlayerInfo } = {};
        publisherList = {};
        
        logger: LoggerWeb;
        dataReport: ZegoDataReport;
        
        testEnvironment = false;
        
        //streamcentersignal
        heartbeatTimer = null;
        heartbeatInterval = 10 * 1000;
        
        //,3s
        qualityTimerInterval = 3 * 1000;
        
        maxRetryCount = 5;
        
        previewVideoList: ZegoPreview[] = [];
        
        signalList: { [index: string]: SignalInfo } = {};
        
        
        appid: number;
        userid: string;
        token: string;
        
        
        constructor (log: LoggerWeb) {
                super (log);
                this.logger = log;
                this.dataReport = new ZegoDataReport (this.logger);
        }
        
        
        onSignalDisconnected (server) {
        };
        
        /*
         *    "zsc.qmc.0": "ZegoStreamCenter.setQualityMonitorCycle"
         */
        setQualityMonitorCycle (timeInMs: number) {
                this.logger.debug ("zsc.qmc.0 timeInterval " + timeInMs);
                
                this.qualityTimerInterval = timeInMs;
        };
        
        /*
         *    "zsc.ssi.0": "ZegoStreamCenter.setSessionInfo"
         */
        setSessionInfo (appid: number, userid: string, token: string, testEnvironment: boolean) {
                this.logger.debug ("zsc.ssi.0 called");
                
                // this.signal.setSessionInfo(appid, userid, serverUrl);
                this.appid = appid;
                this.userid = userid;
                this.token = token;
                this.testEnvironment = testEnvironment;
        };
        
        onPlayStateUpdate (type, streamid, error) {
        };
        
        onPlayQualityUpdate (streamid, streamQuality) {
        };
        
        onPublishStateUpdate (type, streamid, error) {
        };
        
        onPublishQualityUpdate (streamid, streamQuality) {
        };
        
        
        /*
         *    "zsc.uhb.0": "ZegoStreamCenter.onUpdateHeartBeartIntervalHandle"
         */
        onUpdateHeartBeartIntervalHandle (interval: number) {
                if (interval != this.heartbeatInterval) {
                        this.logger.debug ("zsc.uhb.0 update " + interval);
                        
                        if (this.heartbeatTimer) {
                                clearTimeout (this.heartbeatTimer);
                                this.heartbeatTimer = null;
                        }
                        
                        this.heartbeatInterval = interval;
                        
                        this.startSignalHeartbeat ();
                }
        };
        
        
        /*
         *    "zsc.em.0": "ZegoStreamCenter.enableMicrophone"
         */
        enableMicrophone (localVideo: HTMLElement, enable: boolean) {
                let preview = this.checkPreivew (localVideo);
                if (!preview) {
                        this.logger.info ("zsc.em.0 no preview");
                        return false;
                }
                
                return preview.enableMicrophone (enable);
        };
        
        /*
         *    "zsc.ec.0": "ZegoStreamCenter.enableCamera"
         */
        enableCamera (localVideo: HTMLElement, enable: boolean) {
                let preview = this.checkPreivew (localVideo);
                if (!preview) {
                        this.logger.error ("zsc.ec.0 no preview");
                        return false;
                }
                
                return preview.enableCamera (enable);
        };
        
        /*
         *    "zsc.sp.0": "ZegoStreamCenter.startPreview"
         */
        startPreview (localVideo: HTMLElement, mediaStreamConstraints: MediaStreamConstraints, success: Function, error: Function): boolean {
                if (!localVideo) {
                        this.logger.error ("zsc.sp.0 localVideo null");
                        return false;
                }
                
                let preview = this.checkPreivew (localVideo);
                
                if (preview) {
                        this.logger.warn ("zsc.sp.0 localvideo alredy exist");
                        return true;
                }
                
                preview = new ZegoPreview (this.logger);
                this.previewVideoList.push (preview);
                preview.startPreview (localVideo, mediaStreamConstraints, success, error);
                
                this.logger.debug ("zsc.sp.0 call success");
                return true;
        };
        
        /*
         *    "zsc.sp.1": "ZegoStreamCenter.stopPreview"
         */
        stopPreview (localVideo: HTMLElement): boolean {
                
                if (!localVideo) {
                        this.logger.warn ("zsc.sp.0 localVideo null");
                        return false;
                }
                
                for (let streamid in this.publisherList) {
                        if (this.publisherList[streamid].localVideo === localVideo) {
                                this.publisherList[streamid].localVideo = null;
                        }
                }
                
                let preview = this.checkPreivew (localVideo);
                if (!preview) {
                        this.logger.warn ("zsc.sp.0 no preview");
                        return false;
                }
                if (preview.previewSuc) {
                        preview.stopPreview ();
                        this.removePreview (preview);
                }
                
                
                return true;
        };
        
        /*
         *    "zsc.pss.0": "ZegoStreamCenter.setPublishStateStart"
         */
        setPublishStateStart (streamid, localVideo) {
                let totalStreamId = this.getTotalStreamId (streamid);
                let publish = this.publisherList[totalStreamId];
                if (publish) {
                        this.logger.error ("zsc.pss.0 publisher already exist");
                        return false;
                }
                
                let publisher = new ZegoPublish (this.logger, null, this.dataReport, this.qualityTimerInterval);
                publisher.onPublishStateUpdate = (type, _streamid, error) => {
                        const publish = this.publisherList[_streamid];
                        if (publish) {
                                this.onPublishStateUpdate (type, publish.streamId, error);
                        }
                        else {
                                this.logger.error ("zsc.psuh.0 cannot find publish " + streamid);
                        }
                        
                };
                publisher.onPublishQualityUpdate = (_streamid, streamQuality) => {
                        const publish = this.publisherList[_streamid];
                        if (publish) {
                                this.onPublishQualityUpdate (publish.streamId, streamQuality)
                        }
                        else {
                                this.logger.error ("zsc.psuh.0 cannot find publish " + streamid);
                        }
                        
                };
                
                this.publisherList[totalStreamId] = {
                        localVideo: localVideo,
                        publisher: publisher,
                        serverUrls: [],
                        retryCount: 0,
                        streamId: streamid,
                };
                
                this.dataReport.eventStart (publisher.reportSeq, "GetSignalUrl");
                
                return true;
        };
        
        /*
       *    "zsc.gts.0": "ZegoStreamCenter.getTotalStreamId"
       */
        getTotalStreamId (streamid) {
                if (this.testEnvironment) {
                        var testStreamId = "zegotest-" + this.appid + "-" + streamid;
                        this.logger.info ("zsc.gts.0 test streamid " + testStreamId);
                        return testStreamId;
                }
                
                return streamid;
        }
        
        /*
         *    "zsc.sps.0": "ZegoStreamCenter.startPublishingStream"
         */
        startPublishingStream (streamid: string, serverUrls: string[], preferPublishSourceType?: number) {
                
                this.logger.debug ("zsc.sps.0 call");
                
                let totalStreamid = this.getTotalStreamId (streamid);
                let publish = this.publisherList[totalStreamid];
                if (!publish) {
                        this.logger.error ("zsc.sps.0 publisher don't exist");
                        return false;
                }
                
                let publisher = publish.publisher;
                this.dataReport.eventEndWithMsg (publisher.reportSeq, "GetSignalUrl", {
                        urls: serverUrls
                });
                
                if (!serverUrls || serverUrls.length === 0) {
                        this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.error, streamid, publishErrorList.DISPATCH_ERROR);
                        
                        this.logger.info ("zsc.sps.0 server don't have signal url");
                        return false;
                }
                
                let serverUrl = serverUrls[0];
                publish.serverUrls = [...publish.serverUrls, ...serverUrls];
                
                return this.connectPublishServer (totalStreamid, serverUrl);
        };
        
        
        updateWaitingList (signalInfo: SignalInfo, isPublish: boolean, streamId: string, success: Function, error: Function) {
                if (isPublish) {
                        signalInfo.publishWaitingList.push ({
                                streamId: streamId,
                                success: success,
                                error: error
                        });
                } else {
                        signalInfo.playWaitingList.push ({
                                streamId: streamId,
                                success: success,
                                error: error
                        });
                }
        }
        
        
        /*
         *    "zsc.ps.0": "ZegoStreamCenter.publishStream"
         */
        publishStream (streamid) {
                let publisher: ZegoPublish = this.publisherList[streamid].publisher;
                if (!publisher) {
                        this.logger.info ("zsc.ps.0 publisher don't exist");
                        return;
                }
                
                let localStream = null;
                let videoInfo = null;
                let preview = this.checkPreivew (this.publisherList[streamid].localVideo);
                if (preview) {
                        localStream = preview.localStream;
                        videoInfo = preview.videoInfo;
                }
                
                if (!localStream) {
                        this.logger.info ("zsc.ps.0 no localStream");
                }
                
                this.logger.debug ("zsc.ps.0 call success");
                publisher.startPublish (streamid, localStream, videoInfo);
        }
        
        connectPublishServer (streamId: string, serverUrl: string) {
                
                let publish = this.publisherList[streamId];
                if (!publish) {
                        this.logger.error ("zsc.cps.0 publisher don't exist");
                        return false;
                }
                
                this.dataReport.eventStart (publish.publisher.reportSeq, "ConnectServer");
                
                this.connetWithReuseSignalServer (streamId, true, serverUrl, (streamid: string, signalInfo: SignalInfo) => {
                        
                        //check streamid exist
                        let checkPublish = this.publisherList[streamid];
                        if (!checkPublish) {
                                this.logger.info ("zsc.cps.0 after connect publisher don't exist");
                                return;
                        }
                        
                        let checkPublisher = checkPublish.publisher;
                        if (!checkPublisher) {
                                this.logger.info ("zsc.cps.1 check publisher don't exist");
                                return;
                        }
                        
                        this.dataReport.eventEndWithMsg (checkPublisher.reportSeq, "ConnectServer", {
                                result: 0,
                                server: serverUrl
                        });
                        
                        let tokenInfo = signalInfo.tokenInfo;
                        this.logger.info ("zsc.cps.0 update token success");
                        
                        if (tokenInfo && tokenInfo.report) {
                                checkPublisher.qualityUpload = tokenInfo.report;
                                checkPublisher.qualityUploadInterval = tokenInfo.report_interval;
                        }
                        
                        checkPublisher.signal = signalInfo.signal;
                        
                        checkPublish.retryCount = 0;
                        this.publishStream (streamid);
                        
                        this.getTokenSuccess ();
                        
                }, (streamid, result) => {
                        this.logger.error ("zsc.cps.0 update token failed " + result);
                        
                        //check streamid exist
                        let checkPublish = this.publisherList[streamid];
                        if (!checkPublish) {
                                this.logger.info ("zsc.cps.0 after connect publisher don't exist");
                                return;
                        }
                        
                        if (this.shouldRetry (checkPublish, result)) {
                                this.logger.info ("zsc.cps.1 retry connect");
                                
                                let retryServerUrl = checkPublish.serverUrls[0];
                                checkPublish.serverUrls.splice (0, 1);
                                
                                checkPublish.retryCount += 1;
                                this.connectPublishServer (streamid, retryServerUrl);
                        }
                        else {
                                this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.error, streamid, publishErrorList.TOKEN_ERROR);
                        }
                });
                
                return true;
        }
        
        shouldRetry (stream: { serverUrls: string[], retryCount: number }, errorCode: number) {
                if (stream.serverUrls.length == 0) {
                        return false;
                }
                
                if (stream.retryCount >= this.maxRetryCount) {
                        return false;
                }
                
                if (errorCode != 3) {
                        return false;
                }
                
                return true;
        }
        
        /*
         *    "zsc.gts.0": "ZegoStreamCenter.getTokenSuccess"
         */
        getTokenSuccess () {
                this.logger.debug ("zsc.gts.0 call");
        }
        
        /*
         *    "zsc.sps.0.1": "ZegoStreamCenter.stopPublishingStream"
         */
        stopPublishingStream (streamid: string) {
                
                let totalStreamId = this.getTotalStreamId (streamid);
                let publish = this.publisherList[totalStreamId];
                
                if (!publish) {
                        this.logger.warn ("zsc.sps.0.1 publisher don't exist");
                        return;
                }
                
                if (publish.publisher) {
                        publish.publisher.stopPublish ();
                        delete publish.publisher;
                }
                
                //update signal
                this.removeStreamFromSignal (true, totalStreamId);
                this.stopSignalHeartbeat ();
                
                delete this.publisherList[totalStreamId];
                
                this.logger.debug ("zsc.sps.0.1 call success");
        };
        
        
        /*
         *    "zsc.psao.1": "ZegoStreamCenter.setPlayStreamAudioOutput"
         */
        setPlayStreamAudioOutput (streamid: string, audioOutput: string) {
                let totalStreamId = this.getTotalStreamId (streamid);
                if (audioOutput != undefined && audioOutput.length != 0) {
                        this.logger.debug ("zsc.psao.1 device " + audioOutput);
                        let play = this.playerList[totalStreamId];
                        if (!play) {
                                this.logger.info ("zsc.psao.1 play don't exist");
                                return false;
                        }
                        
                        if (!play.player) {
                                this.logger.info ("zsc.psao.1 player don't exist");
                                return false;
                        }
                        
                        return play.player.setAudioDestination (audioOutput);
                }
                
                return false;
        };
        
        /*
         *    "zsc.psao.0": "ZegoStreamCenter.setPublishStreamAudioOutput"
         */
        setPublishStreamAudioOutput (localVideo: HTMLElement, audioOutput: string): boolean {
                
                if (audioOutput != undefined && audioOutput.length != 0 && localVideo) {
                        this.logger.debug ("zsc.psao.0 device " + audioOutput);
                        
                        let preview = this.checkPreivew (localVideo);
                        if (preview) {
                                preview.setAudioDestination (audioOutput);
                        }
                        else {
                                this.logger.info ("zsc.psao.0 no preview");
                        }
                }
                
                return false;
        };
        
        
        /*
          *    "zsc.crss.0": "ZegoStreamCenter.connetWithReuseSignalServer"
          */
        connetWithReuseSignalServer (streamId: string, isPublish: boolean, serverUrl: string, success: Function, error: Function) {
                this.logger.debug ("zsc.crss.0 begin " + serverUrl);
                
                let signalInfo = null;
                if (this.signalList[serverUrl]) {
                        signalInfo = this.signalList[serverUrl];
                        //already connected
                        if (signalInfo.state == ENUM_SIGNAL_STATE.connected) {
                                this.logger.debug ("zsc.crss.0 already connected " + serverUrl + " streamId: " + streamId);
                                
                                if (isPublish) {
                                        signalInfo.publishConnectedList.push (streamId);
                                } else {
                                        signalInfo.playConnectedList.push (streamId);
                                }
                                
                                success (streamId, signalInfo);
                        } else if (signalInfo.state == ENUM_SIGNAL_STATE.connecting) { //isConnecting
                                this.logger.debug ("zsc.crss.0 signal is connecting " + serverUrl + " streamId: " + streamId);
                                this.updateWaitingList (signalInfo, isPublish, streamId, success, error);
                        }
                } else {
                        //no connect
                        this.logger.debug ("zsc.crss.0 new signal " + serverUrl + " streamId: " + streamId);
                        
                        let signal = new ZegoSignal (this.logger);
                        signal.setSessionInfo (this.appid, this.userid);
                        signal.onUpdateHeartBeartInterval = this.onUpdateHeartBeartIntervalHandle;
                        signal.onDisconnect = this.onDisconnectHandle;
                        
                        this.signalList[serverUrl] = {
                                signal: signal,
                                state: ENUM_SIGNAL_STATE.connecting,
                                publishWaitingList: [],
                                playWaitingList: [],
                                publishConnectedList: [],
                                playConnectedList: [],
                                tokenInfo: null
                        };
                        
                        this.updateWaitingList (this.signalList[serverUrl], isPublish, streamId, success, error);
                        
                        signal.connectServer (this.token, serverUrl, (result, server, tokenInfo) => {
                                signalInfo = this.signalList[serverUrl];
                                
                                let i = 0;
                                let publishCallback;
                                let playCallback;
                                if (result != 0) {
                                        //connected failed, notify and delete
                                        this.logger.debug ("zsc.crss.0 connect failed " + server);
                                        
                                        for (i = 0; i < signalInfo.publishWaitingList.length; i++) {
                                                publishCallback = signalInfo.publishWaitingList[i];
                                                if (publishCallback.error) {
                                                        publishCallback.error (publishCallback.streamId, result);
                                                }
                                        }
                                        
                                        for (i = 0; i < signalInfo.playWaitingList.length; i++) {
                                                playCallback = signalInfo.playWaitingList[i];
                                                if (playCallback.error) {
                                                        playCallback.error (playCallback.streamId, result);
                                                }
                                        }
                                        
                                        delete this.signalList[serverUrl];
                                } else {
                                        //connected success, notify and update state
                                        this.logger.debug ("zsc.crss.0 connected success " + server);
                                        
                                        signalInfo.state = ENUM_SIGNAL_STATE.connected;
                                        signalInfo.tokenInfo = tokenInfo;
                                        
                                        for (i = 0; i < signalInfo.publishWaitingList.length; i++) {
                                                publishCallback = signalInfo.publishWaitingList[i];
                                                if (publishCallback.success) {
                                                        publishCallback.success (publishCallback.streamId, signalInfo);
                                                }
                                                
                                                signalInfo.publishConnectedList.push (publishCallback.streamId);
                                        }
                                        
                                        for (i = 0; i < signalInfo.playWaitingList.length; i++) {
                                                playCallback = signalInfo.playWaitingList[i];
                                                if (playCallback.success) {
                                                        playCallback.success (playCallback.streamId, signalInfo);
                                                }
                                                
                                                signalInfo.playConnectedList.push (playCallback.streamId);
                                        }
                                        
                                        signalInfo.publishWaitingList = [];
                                        signalInfo.playWaitingList = [];
                                        
                                        if (this.heartbeatTimer == null) this.startSignalHeartbeat ();
                                }
                        });
                }
        }
        
        /*
         *    "zsc.pss.1": "ZegoStreamCenter.setPlayStateStart"
         */
        setPlayStateStart (streamid: string, remoteVideo: HTMLElement, audioOutput: string, playOption: PlayOption) {
                let totalStreamId = this.getTotalStreamId (streamid);
                let play = this.playerList[totalStreamId];
                if (play) {
                        this.logger.warn ("zsc.pss.1 player already exist");
                        return false;
                }
                
                let player = new ZegoPlayWeb (this.logger, null, this.dataReport, this.qualityTimerInterval);
                player.onPlayStateUpdate = this.onPlayStateUpdate;
                player.onPlayQualityUpdate = this.onPlayQualityUpdate;
                player.onVideoSizeChanged = this.onVideoSizeChanged;
                
                this.playerList[totalStreamId] = {
                        player: player,
                        remoteVideo: remoteVideo,
                        audioOutput: audioOutput,
                        signal: null,
                        serverUrls: [],
                        retryCount: 0,
                        playOption: playOption
                };
                
                this.dataReport.eventStart (player.reportSeq, "GetSignalUrl");
                
                return true;
        };
        
        /*
         *    "zsc.sps.1": "ZegoStreamCenter.startPlayingStream"
         */
        startPlayingStream (streamid: string, serverUrls: string[], currentPlaySourceType?: number): boolean {
                this.logger.debug ("zsc.sps.1 start play called");
                let totalStreamId = this.getTotalStreamId (streamid);
                let play = this.playerList[totalStreamId];
                if (!play) {
                        this.logger.error ("zsc.sps.1 player don't exist");
                        return false;
                }
                
                let player = play.player;
                this.dataReport.eventEndWithMsg (player.reportSeq, "GetSignalUrl", {
                        urls: serverUrls
                });
                
                if (serverUrls.length == 0) {
                        this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.error, streamid, playErrorList.DISPATCH_ERROR);
                        
                        this.logger.info ("zsc.sps.1 server don't have signal url");
                        return false;
                }
                
                play.serverUrls = [...play.serverUrls, ...serverUrls];
                
                return this.connectPlayServer (totalStreamId, serverUrls[0]);
        };
        
        
        /*
         *    "zsc.cps.1": "ZegoStreamCenter.connectPlayServer"
         */
        private connectPlayServer (streamId: string, serverUrl: string): boolean {
                let play = this.playerList[streamId];
                if (!play) {
                        this.logger.error ("zsc.cps.1 player don't exist");
                        return false;
                }
                
                this.dataReport.eventStart (play.player.reportSeq, "ConnectServer");
                this.connetWithReuseSignalServer (streamId, false, serverUrl, (streamid: string, signalInfo: SignalInfo) => {
                        //check streamid exist
                        let checkPlay = this.playerList[streamid];
                        if (!checkPlay) {
                                this.logger.error ("zsc.cps.1 after connect player don't exist");
                                return;
                        }
                        
                        let checkPlayer = checkPlay.player;
                        if (!checkPlayer) {
                                this.logger.error ("zsc.cps.1 checkplayer don't exist");
                                return;
                        }
                        
                        this.dataReport.eventEndWithMsg (checkPlayer.reportSeq, "ConnectServer", {
                                result: 0,
                                server: serverUrl
                        });
                        
                        let tokenInfo = signalInfo.tokenInfo;
                        this.logger.info ("zsc.cps.1 update token success");
                        
                        if (tokenInfo && tokenInfo.report) {
                                checkPlayer.qualityUpload = tokenInfo.report;
                                checkPlayer.qualityUploadInterval = tokenInfo.report_interval;
                        }
                        
                        checkPlayer.signal = signalInfo.signal;
                        
                        checkPlay.retryCount = 0;
                        this.playStream (streamid);
                        
                        this.getTokenSuccess ();
                }, (streamid, result) => {
                        let checkPlay = this.playerList[streamid];
                        if (!checkPlay) {
                                this.logger.error ("zsc.cps.1 after connect player don't exist");
                                return;
                        }
                        
                        if (this.shouldRetry (checkPlay, result)) {
                                this.logger.info ("zsc.cps.1 retry connect");
                                
                                let retryServerUrl = checkPlay.serverUrls[0];
                                checkPlay.serverUrls.splice (0, 1);
                                
                                checkPlay.retryCount += 1;
                                this.connectPlayServer (streamid, retryServerUrl);
                        }
                        else {
                                this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.error, streamid, playErrorList.TOKEN_ERROR);
                        }
                        
                });
                
                return true;
        }
        
        
        /*
         *    "zsc.ps.1": "ZegoStreamCenter.playStream"
         */
        private playStream (streamid) {
                const player: ZegoPlayWeb = this.playerList[streamid].player;
                if (!player) {
                        this.logger.warn ("zsc.ps.1 player don't exist");
                        return;
                }
                
                this.logger.debug ("zsc.ps.1 call success");
                player.startPlay (streamid, this.playerList[streamid].remoteVideo, this.playerList[streamid].audioOutput, this.playerList[streamid].playOption);
        }
        
        
        /*
         *    "zsc.rsfs.0": "ZegoStreamCenter.removeStreamFromSignal"
         */
        private removeStreamFromSignal (isPublish, streamId) {
                
                let deleteSignal = [];
                for (let serverUrl in this.signalList) {
                        let signalInfo = this.signalList[serverUrl];
                        if (isPublish) {
                                for (let i = 0; i < signalInfo.publishConnectedList.length; i++) {
                                        if (signalInfo.publishConnectedList[i] === streamId) {
                                                this.logger.debug ("zsc.rsfs.0 found from publish");
                                                signalInfo.publishConnectedList.splice (i, 1);
                                                break;
                                        }
                                }
                        }
                        else {
                                for (let j = 0; j < signalInfo.playConnectedList.length; j++) {
                                        if (signalInfo.playConnectedList[j] === streamId) {
                                                this.logger.debug ("zsc.rsfs.0 found from play");
                                                signalInfo.playConnectedList.splice (j, 1);
                                                break;
                                        }
                                }
                        }
                        
                        if (signalInfo.publishConnectedList.length == 0 && signalInfo.playConnectedList.length == 0) {
                                signalInfo.signal.disconnectServer ();
                                deleteSignal.push (serverUrl);
                        }
                }
                
                for (let k = 0; k < deleteSignal.length; k++) {
                        delete this.signalList[deleteSignal[k]];
                }
        }
        
        
        /*
         *    "zsc.ssh.1": "ZegoStreamCenter.stopSignalHeartbeat"
         */
        private stopSignalHeartbeat () {
                this.logger.debug ("zsc.ssh.1 call");
                
                let count = 0;
                for (let url in this.signalList) {
                        count += 1;
                }
                
                if (this.heartbeatTimer && count == 0) {
                        
                        this.logger.info ("zsc.ssh.1 stop");
                        
                        clearTimeout (this.heartbeatTimer);
                        
                        this.heartbeatTimer = null;
                }
        }
        
        
        /*
         *    "zsc.sps.1.1": "ZegoStreamCenter.stopPlayingStream"
         */
        stopPlayingStream (streamid) {
                let totalStreamId = this.getTotalStreamId (streamid);
                let player = this.playerList[totalStreamId];
                if (!player) {
                        this.logger.info ("zsc.sps.1.1 player don't exist");
                        return;
                }
                
                if (player.player) {
                        player.player.stopPlay ();
                        delete player.player;
                }
                
                //update signal
                this.removeStreamFromSignal (false, totalStreamId);
                this.stopSignalHeartbeat ();
                
                delete this.playerList[totalStreamId];
                
                this.logger.debug ("zsc.sps.1.1 call success");
        };
        
        reset () {
                
                for (let publishStreamId in this.publisherList) {
                        if (this.publisherList[publishStreamId].publisher) {
                                this.publisherList[publishStreamId].publisher.stopPublish ();
                        }
                }
                
                for (let playStreamId in this.playerList) {
                        if (this.playerList[playStreamId].player) {
                                this.playerList[playStreamId].player.stopPlay ();
                        }
                }
                
                for (let serverUrl in this.signalList) {
                        if (this.signalList[serverUrl].signal) {
                                this.signalList[serverUrl].signal.disconnectServer ();
                        }
                }
                
                this.playerList = {};
                this.publisherList = {};
                this.signalList = {};
                
                if (this.heartbeatTimer) {
                        clearTimeout (this.heartbeatTimer);
                        this.heartbeatTimer = null;
                }
        };
        
        checkMessageTimeout = () => {
                for (let serverUrl in this.signalList) {
                        if (this.signalList[serverUrl].signal) {
                                this.signalList[serverUrl].signal.checkMessageTimeout ();
                        }
                }
        };
        
        getAllInUseUrl = () => {
                let serverUrls = [];
                
                for (let serverUrl in this.signalList) {
                        serverUrls.push (serverUrl);
                }
                
                return serverUrls;
        };
        
        /*
         *    "zsc.od.0": "ZegoStreamCenter.onDisconnectHandle"
         */
        onDisconnectHandle = (server) => {
                this.logger.info ("zsc.od.0 call");
                
                if (this.signalList[server]) {
                        let signalInfo = this.signalList[server];
                        
                        for (let i = 0; i < signalInfo.publishConnectedList.length; i++) {
                                let publish = this.publisherList[signalInfo.publishConnectedList[i]];
                                if (publish && publish.publisher) {
                                        publish.publisher.onDisconnect ();
                                }
                        }
                        
                        for (let i = 0; i < signalInfo.playConnectedList.length; i++) {
                                let play = this.playerList[signalInfo.playConnectedList[i]];
                                if (play && play.player) {
                                        play.player.onDisconnect ();
                                }
                        }
                        
                        delete this.signalList[server];
                        
                        this.stopSignalHeartbeat ();
                }
        };
        
        
        startSignalHeartbeat () {
                this.logger.debug ("zsc.ssh.0 call");
                
                if (this.heartbeatTimer) {
                        clearTimeout (this.heartbeatTimer);
                        this.heartbeatTimer = null;
                }
                
                this.heartbeatTimer = setTimeout (() => {
                        this.checkSignalHeartbeat ();
                }, this.heartbeatInterval);
        }
        
        checkSignalHeartbeat () {
                this.logger.debug ("zsc.csh.0 call");
                
                for (let streamUrl in this.signalList) {
                        if (this.signalList[streamUrl].signal) {
                                this.signalList[streamUrl].signal.sendHeartbeat ();
                        }
                }
                
                if (this.heartbeatTimer) this.startSignalHeartbeat ();
        }
        
        checkPreivew (localVideo: HTMLElement) {
                for (let i = 0; i < this.previewVideoList.length; i++) {
                        if (this.previewVideoList[i].localVideo === localVideo) {
                                return this.previewVideoList[i];
                        }
                }
                
                return null;
        }
        
        removePreview (preview: ZegoPreview) {
                for (let i = 0; i < this.previewVideoList.length; i++) {
                        if (this.previewVideoList[i] === preview) {
                                this.previewVideoList.splice (i, 1);
                                break;
                        }
                }
        }
        
        onPlayerStreamUrlUpdate (streamid: string, url: string, type: string) {
        
        }
        
        onVideoSizeChanged (streamId: string, videoWidth: number, videoHeight: number) {
        
        }
        
}
