import {Logger} from "./zego.logger";
import {ERRO, StreamQuality} from "./zego.entity";

export abstract class ZegoStreamCenter {
        
        constructor (log: Logger) {
        
        }
        
        abstract stopPlayingStream (streamId: string);
        
        abstract reset ();
        
        abstract startPlayingStream (streamid: string, serverUrls: string[], currentPlaySourceType?: number): boolean;
        
        abstract startPublishingStream (streamid: string, serverUrls: string[], preferPublishSourceType?: number);
        
        abstract onPlayStateUpdate (type: 0 | 1 | 2, streamid: string, error: ERRO);
        
        abstract onPlayQualityUpdate (streamId: string, streamQuality: StreamQuality);
        
        abstract onPublishStateUpdate (type: 0 | 1 | 2, streamid: string, error: ERRO);
        
        abstract onPublishQualityUpdate (streamId: string, streamQuality: StreamQuality) ;
        
        abstract onPlayerStreamUrlUpdate (streamid: string, url: string, type: string);
        
        abstract onVideoSizeChanged (streamId: string, videoWidth: number, videoHeight: number);
        
        
        
        setSessionInfo (appid: number, userid: string, token: string, testEnvironment: boolean) {
        };
}


