import {D, Logger} from "../common/zego.logger";
import {ENUM_LOG_LEVEL} from "../common/zego.entity";

export class LoggerWeb extends Logger {
        
        openWebSocketLogServer (url: string) {
                if (this.url != url) {
                        this.url = url;
                        this.stopWebSocketServer ();
                        if (!url) return;
                        
                        this.websocket = new WebSocket (url);
                        
                        
                        this.websocket.onopen = function (evt) {
                        
                        };
                        this.websocket.onclose = function (evt) {
                        
                        };
                        this.websocket.onmessage = function (evt) {
                        
                        };
                        this.websocket.onerror = function (err) {
                                console.error (`open log websocket error:${err}`);
                        };
                }
                
        }
        
        SendHttpsLog () {
                if (this.logCacheSend.length == 0) {
                        return;
                }
                let uploadData = this.logCacheSend.join ("\n");
                
                //console.log("url " + this.url);
                //console.log(uploadData);
                let xmlhttp = new XMLHttpRequest ();
                xmlhttp.onreadystatechange = () => {
                        if (xmlhttp.readyState == 4) {
                                if (xmlhttp.status == 200) {
                                        if (xmlhttp.responseText.length == 0) {
                                                return;
                                        }
                                        
                                        try {
                                                let json = JSON.parse (xmlhttp.responseText);
                                                let interval = json.interval;
                                                if (typeof interval === "number" && this.logUploadInterval !== interval) {
                                                        this.timeInterval = interval;
                                                        this.openHttpsLogServer (this.url);
                                                }
                                        }
                                        catch (e) {
                                                console.log ("send result failed " + e);
                                        }
                                        
                                }
                                else {
                                        console.log ("send failed " + xmlhttp.status);
                                }
                        }
                };
                
                xmlhttp.open ("POST", this.url, true);
                xmlhttp.send (uploadData);
                
                
                this.logCacheSend = [];
        }
        
        logReportParamList (level: ENUM_LOG_LEVEL, logInfo: string) {
                
                const t = new Date ();
                let stringTime = t.getFullYear () + "/";
                stringTime += (D[t.getMonth () + 1] || t.getMonth () + 1) + "/";
                stringTime += (D[t.getDate ()] || t.getDate ()) + " ";
                stringTime += (D[t.getHours ()] || t.getHours ()) + ":";
                stringTime += (D[t.getMinutes ()] || t.getMinutes ()) + ":";
                stringTime += (D[t.getSeconds ()] || t.getSeconds ());
                stringTime += "." + t.getTime () % 1000;
                
                logInfo["time"] = stringTime;
                logInfo["level"] = level;
                
                
                logInfo["console"] = "rtc";
                
                
                logInfo["appid"] = this.appid;
                logInfo["roomid"] = this.roomid;
                logInfo["userid"] = this.userid;
                logInfo["id_name"] = this.userid;
                logInfo["userName"] = this.userName;
                logInfo["sessionid"] = this.sessionid;
                logInfo["version"] = this.version;
                
                return [JSON.stringify (logInfo)];
        }
}