import {ENUM_PLAY_STATE, ENUM_RETRY_STATE, PlayOption} from "../common/zego.entity";
import {ZegoDataReport} from "../common/zego.datareport";
import {ZegoSignal} from "../common/zego.signal";
import {ENUM_PLAY_STATE_UPDATE, getSeq, playErrorList, publishErrorList} from "../common/zego.extern";
import {Logger} from "../common/zego.logger";

export class ZegoPlayWeb {
        logger: Logger;
        signal: ZegoSignal;
        state = ENUM_PLAY_STATE.stop;
        
       
       
        candidateInfo = [];
        
        waitICETimer = null;
        waitingICETimeInterval = 5000;
        
        waitingOfferTimer = null;
        waitingOfferTimeInterval = 5000;
        
        waitingServerTimer = null;
        waitingServerTimerInterval = 3000;
        
        // waitingAnswerTimer: number;
        // waitingAnswerTimeInterval = 0;
        
        qualityTimer = null;
        qualityTimeInterval: number;
        
        
        playQualityList = [];
        maxQualityListCount = 10;
        lastPlayStats = {
                time: 0,
                audioBytesReceived: 0,
                videoBytesReceived: 0,
                framesDecoded: 0,
                framesReceived: 0,
                framesDropped: 0
        };
        
        dataReport: ZegoDataReport;
        reportSeq = getSeq ();
        
        videoSizeCallback = false;
        
        qualityUpload = false;
        qualityUploadInterval = 30 * 1000;
        qualityUploadLastTime = 0;
        
        //retry
        maxRetryCount = 3;
        currentRetryCount = 0;
        retryState = ENUM_RETRY_STATE.didNotStart;
       
        
        
        remoteVideo: any;
        streamId: string;
        audioOutput: any;
        sessionId: number;
        sessionSeq: number;
        answerSeq: number;
        audioOputput: any;
        
        peerConnection: RTCPeerConnection | any;
       
        
        playOption:{
                 playType:'audio'|'video'|'all'
         };
        closeSessionSignal = false;
        
        constructor (log: Logger, signal: ZegoSignal, dataReport: ZegoDataReport, qualityTimeInterval: number) {
                
                this.logger = log;
                this.signal = signal;
                this.dataReport = dataReport;
                this.qualityTimeInterval = qualityTimeInterval;
                dataReport.newReport (this.reportSeq);
        }
        
        
        /*
         *    "zp.sad.1": "ZegoPlayWeb.setAudioDestination"
         */
        setAudioDestination (audioOutput: string) {
                if (!this.remoteVideo) {
                        this.logger.info ("zp.sad.1 no remoteVideo");
                        return false;
                }
                
                if (this.remoteVideo.sinkId !== 'undefined') {
                        this.remoteVideo.setSinkId (audioOutput).then (() => {
                                this.logger.info ("zp.sad.1 success device: " + audioOutput);
                                // this.audioOutput = audioOutput;
                        }).catch ( (error)=> {
                                this.logger.info ("zp.sad.1 " + error.name);
                        });
                        return true;
                } else {
                        this.logger.error ("zp.sad.1 browser does not suppport");
                        return false;
                }
        };
        
        /*
         *    "zp.sp.1": "ZegoPlayWeb.startPlay"
         */
        startPlay (streamId: string, remoteVideo: HTMLElement, audioOutput?: string,playOption?:PlayOption) {
                this.logger.debug ("zp.sp.1 called ", streamId);
                
                if (!streamId) {
                        this.logger.warn ("zp.sp.1 streamId is null");
                        return;
                }
                
                this.streamId = streamId;
                this.remoteVideo = remoteVideo;
                this.audioOutput = audioOutput;
                this.playOption = playOption;
                
                //create session
                this.sessionSeq = getSeq ();
                this.dataReport.eventStart (this.reportSeq, "CreateSession");
                this.signal.createSession (this.sessionSeq, 0, streamId, (seq, sessionId, data) => {
                        this.dataReport.eventEndWithMsg (this.reportSeq, "CreateSession", {
                                sessionId: data.session_id
                        });
                        
                        this.logger.info('zp.sp.1 sessionId:'+data.session_id);
                        
                        if (this.sessionSeq != seq) {
                                this.logger.error ("zp.sp.1 seq is not match.");
                                return;
                        }
                        
                        if (data.result !== 0) {
                                this.logger.error ("zp.sp.1 create error");
                                this.playStateUpdateError (playErrorList.CREATE_SESSION_ERROR);
                        } else {
                                this.sessionId = data.session_id;
                                this.onCreatePlaySessionSuccess (data);
                        }
                        
                }, (err, seq) => {
                        this.dataReport.eventEndWithMsg (this.reportSeq, "CreateSession", {
                                error: err
                        });
                        
                        this.playStateUpdateError (playErrorList.SEND_SESSION_TIMEOUT);
                });
                
                this.state = ENUM_PLAY_STATE.waitingSessionRsp;
                this.logger.debug ("zp.sp.1 called success");
        };
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // create session result
        /*
         *    "zp.ops.1": "ZegoPlayWeb.onCreatePlaySessionSuccess"
         */
        private onCreatePlaySessionSuccess (data) {
                this.logger.debug ("zp.ops.1 success");
                
                let urls = [];
                if (data.turn_server) urls.push (data.turn_server);
                if (data.stun_server) urls.push (data.stun_server);
                
                const configuration = {
                        iceServers: [{
                                urls: urls,
                                username: data.turn_username,
                                credential: data.turn_auth_key
                        }]
                };
                
                this.logger.info ("zp.ops.1 username: " + data.turn_username);
                this.logger.info ("zp.ops.1 credential: " + data.turn_auth_key);
                
                this.peerConnection = new RTCPeerConnection (configuration);
                this.peerConnection.onicecandidate = (e) => {
                        this.onIceCandidate (e);
                };
                
                this.peerConnection.onsignalingstatechange = (e:Event) => {
                        this.onConnectionStateChange (e);
                };
                
                this.peerConnection.oniceconnectionstatechange = (e) => {
                        this.onIceConnectionStateChange (e);
                };
                
                this.peerConnection.onaddstream = (e) => {
                        this.onGotRemoteStream (e);
                };
                
                this.remoteVideo.oncanplay = () => {
                        this.logger.debug ("zp.ops.1 " + this.remoteVideo.videoWidth + " X " + this.remoteVideo.videoHeight);
                        if (!this.videoSizeCallback) {
                                this.logger.debug ("zp.ops.1 onresize callback");
                                
                                this.onVideoSizeChanged (this.streamId, this.remoteVideo.videoWidth, this.remoteVideo.videoHeight);
                                this.videoSizeCallback = true;
                        }
                };
        
         
                let offerOptions = {
                        offerToReceiveAudio: 1,
                        offerToReceiveVideo: 1,
                };
                if(this.playOption&&this.playOption.playType === 'audio')offerOptions.offerToReceiveVideo = 0;
                if(this.playOption&&this.playOption.playType === 'video')offerOptions.offerToReceiveAudio = 0;
                
                this.logger.info ("zp.ops.1 createOffer: " + offerOptions);
                //create offer
                this.dataReport.eventStart (this.reportSeq, "CreateOffer");
                this.peerConnection.createOffer (offerOptions).then (
                            (desc) => {
                                    this.dataReport.eventEnd (this.reportSeq, "CreateOffer");
                            
                                    this.onCreateOfferSuccess (desc);
                            },
                            (error) => {
                                    this.dataReport.eventEndWithMsg (this.reportSeq, "CreateOffer", {
                                            error: error.toString ()
                                    });
                            
                                    this.logger.error ("zp.ops.0 create offer error " + error.toString ());
                                    this.playStateUpdateError (playErrorList.CREATE_OFFER_ERROR);
                            }
                );
                
                
                //register callback
                this.signal.registerPushCallback ("MediaDescPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvMediaDesc (seq, sessionId, data);
                });
                this.signal.registerPushCallback ("CandidateInfoPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvCandidateInfo (seq, sessionId, data);
                });
                this.signal.registerPushCallback ("CloseSessionPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvCloseSession (seq, sessionId, data);
                });
                // this.signal.registerPushCallback("WebSocketDisconnect", this.sessionId, onDisconnect, this);
                this.signal.registerPushCallback ("SessionResetPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvResetSession (seq, sessionId, data);
                });
                
                this.logger.debug ("zp.ops.1 call success");
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // create offer result
        /*
         *    "zp.oco.1": "ZegoPlayWeb.onCreateOfferSuccess"
         */
        onCreateOfferSuccess (desc: { sdp: any }) {
                this.logger.debug ("zp.oco.1 localSdp " , desc.sdp);
                
                this.dataReport.eventStart (this.reportSeq, "SetLocalDescription");
                this.peerConnection.setLocalDescription (desc).then (
                            () => {
                                    this.dataReport.eventEnd (this.reportSeq, "SetLocalDescription");
                            
                                    this.onSetLocalDescriptionSuccess (desc);
                            },
                            (error) => {
                                    this.logger.error ("zp.oca.1 set error " + error.toString ());
                                    this.dataReport.eventEnd (this.reportSeq, "SetLocalDescription", {
                                            error: error.toString ()
                                    });
                            
                                    this.playStateUpdateError (playErrorList.SET_LOCAL_DESC_ERROR);
                            }
                );
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // setLocalDescription result
        /*
         *    "zp.osd.1": "ZegoPlayWeb.onSetLocalDescriptionSuccess"
         */
        private onSetLocalDescriptionSuccess (desc) {
                this.logger.debug ("zp.osd.1 success");
                
                let mediaDescription = {
                        sdp: desc.sdp
                };
                
                this.answerSeq = getSeq ();
                this.dataReport.eventStart (this.reportSeq, "SendMediaDesc");
                this.signal.sendMediaDesc (this.answerSeq, this.sessionId, 0, mediaDescription, (seq, sessionId, data) => {
                        if (this.answerSeq != seq || this.sessionId != sessionId) {
                                this.logger.error ("zp.osd.1 seq or sessionId is not equal " + this.answerSeq + " " + seq, +" " + this.sessionId + " " + sessionId);
                                return;
                        }
                        
                        this.logger.debug ("zp.osd.1 send success");
                        
                        this.dataReport.eventEnd (this.reportSeq, "SendMediaDesc");
                        
                        this.state = ENUM_PLAY_STATE.waitingServerAnswer;
                        
                        //send candidate
                        this.sendCandidateInfo (this.candidateInfo);
                        this.candidateInfo = [];
                        
                        //setTimer
                        this.waitingOfferTimer = setTimeout (() => {
                                if (this.state == ENUM_PLAY_STATE.waitingOffserRsp) {
                                        this.logger.error ("zp.osd.1 waiting timeout");
                                        this.playStateUpdateError (playErrorList.SERVER_CANDIDATE_TIMEOUT);
                                }
                        }, this.waitingOfferTimeInterval);
                        
                }, (err, seq) => {
                        this.logger.error ("zp.osd.1 failed to send " + err);
                        this.dataReport.eventEndWithMsg (this.reportSeq, "SendMediaDesc", {
                                error: err
                        });
                        
                        this.playStateUpdateError (playErrorList.SEND_MEDIA_DESC_TIMEOUT);
                });
                
                this.state = ENUM_PLAY_STATE.waitingOffserRsp;
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push offer (setRemoteDescription)
        /*
         *    "zp.orm.1": "ZegoPlayWeb.onRecvMediaDesc"
         */
        private onRecvMediaDesc (seq: number, sessionId: number, data: any) {
                this.logger.debug ("zp.orm.1 received ", data);
                
                if (this.state !== ENUM_PLAY_STATE.waitingServerAnswer) {
                        this.logger.error ("zp.orm.1 current state " + this.state + " not allowed");
                        return;
                }
                
                if (this.waitingOfferTimer != null) {
                        clearTimeout (this.waitingOfferTimer);
                        this.waitingOfferTimer = null;
                }
                
                this.dataReport.addEvent (this.reportSeq, "RecvMediaDesc");
                
                this.signal.sendMediaDescAck (seq, this.sessionId, 0);
                
                let offerDescription: RTCSessionDescription = {
                        type: "answer",
                        sdp: data.sdp,
                        toJSON: () => {
                        }
                        
                };
                
                
                
                //setRemoteDescritpion
                this.dataReport.eventStart (this.reportSeq, "SetRemoteDescription");
                this.logger.info ("zp.orm.1 remoteSdp ", offerDescription.sdp);
                this.peerConnection.setRemoteDescription (new RTCSessionDescription (offerDescription)).then (() => {
                        this.dataReport.eventEnd (this.reportSeq, "SetRemoteDescription");
                        this.logger.debug ("zp.orm.1 set success");
                      
                }, (error) => {
                        this.logger.error ("zp.orm.1 set remote error " + error.toString ());
                        
                        this.dataReport.eventEndWithMsg (this.reportSeq, "SetRemoteDescription", {
                                error: error.toString ()
                        });
                        
                        this.playStateUpdateError (playErrorList.SET_REMOTE_DESC_ERROR);
                });
        
                //setTimer
                this.waitICETimer = setTimeout (() => {
                        if (this.state == ENUM_PLAY_STATE.waitingServerICE) {
                                this.logger.error ("zp.orm.1 waiting server timeout");
                                this.playStateUpdateError (playErrorList.SERVER_CANDIDATE_TIMEOUT);
                        }
                }, this.waitingICETimeInterval);
        
        
                this.state = ENUM_PLAY_STATE.waitingServerICE;
                this.logger.debug ("zp.orm.1 call success");
        }
        
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push ICE (addIceCandidate)
        /*
         *    "zp.orci.1": "ZegoPlayWeb.onRecvCandidateInfo"
         */
        private onRecvCandidateInfo (seq: number, sessionId: number, data: any) {
                
                this.logger.debug ("zp.orci.1 received ");
                
                if (this.state != ENUM_PLAY_STATE.waitingServerICE) {
                        this.logger.warn ("zp.orci.1 current state " + this.state + " not allowed");
                        return;
                }
                
                if (this.waitICETimer != null) {
                        clearTimeout (this.waitICETimer);
                        this.waitICETimer = null;
                }
                
                this.dataReport.addEvent (this.reportSeq, "RecvIceCandidate");
                
                this.signal.sendCandidateInfoAck (seq, this.sessionId, 0);
                
                for (let i = 0; i < data.infos.length; i++) {
                        const ice = {
                                sdpMid: data.infos[i].sdpMid,
                                sdpMLineIndex: data.infos[i].sdpMLineIndex,
                                candidate: data.infos[i].candidate
                        };
                        
                        this.logger.debug ("zp.orci.1 candidate " + ice.candidate);
                        
                        this.peerConnection.addIceCandidate (new RTCIceCandidate (ice)).then (() => {
                                this.logger.debug ("zp.orci.1 add success");
                        }, (error) => {
                                this.logger.error ("zp.orci.1 add error " + error.toString ());
                                this.playStateUpdateError (playErrorList.SERVER_CANDIDATE_ERROR);
                        });
                }
                
                this.state = ENUM_PLAY_STATE.connecting;
                
                this.logger.debug ("zp.orci.1 call success");
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // onIceCandidate callback
        /*
         *    "zp.oic.1": "ZegoPlayWeb.onIceCandidate"
         */
        private onIceCandidate (event) {
                this.logger.info ("zp.oic.1 called");
                
                //send candidate to other peer
                if (event.candidate == undefined) {
                        return;
                }
                
                this.logger.debug ("zp.oic.1 candidate " + event.candidate.candidate);
        
                if (this.state < ENUM_PLAY_STATE.waitingServerICE || this.state == ENUM_PLAY_STATE.stop) {
                        //save candidate Info
                        this.logger.debug ("zp.oic.1 cached");
                        
                        this.candidateInfo.push ({
                                candidate: event.candidate.candidate,
                                sdpMid: event.candidate.sdpMid,
                                sdpMLineIndex: event.candidate.sdpMLineIndex
                        });
                } else {
                        this.logger.debug ("zp.oic.1 send");
                        
                        const candidate = {
                                candidate: event.candidate.candidate,
                                sdpMid: event.candidate.sdpMid,
                                sdpMLineIndex: event.candidate.sdpMLineIndex
                        };
                        
                        this.sendCandidateInfo ([candidate]);
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // onIceStateChange callback
        /*
         *    "zp.ocs.1": "ZegoPlayWeb.onConnectionStateChange"
         */
        private onConnectionStateChange (event) {
                this.logger.info ("zp.oisc.1 called "+event.target.signalingState);
        }
        
        /*
         *    "zp.oics.1": "ZegoPlayWeb.onIceConnectionStateChange"
         */
        private onIceConnectionStateChange (event) {
                
                if (this.state == ENUM_PLAY_STATE.stop || this.peerConnection == null) {
                        return;
                }
                
                this.logger.info ("zp.oisc.1  stateChanged " + this.peerConnection.iceConnectionState);
                
                if (this.peerConnection.iceConnectionState === "connected") {
                        this.dataReport.addEvent (this.reportSeq, "IceConnected");
                        
                        if (this.state != ENUM_PLAY_STATE.playing) {
                                this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.start, this.streamId);
                        }
                        
                        this.state = ENUM_PLAY_STATE.playing;
                        if (this.retryState != ENUM_RETRY_STATE.didNotStart) {
                                this.retryState = ENUM_RETRY_STATE.finished;
                                this.currentRetryCount = 0;
                        }
                        
                        //play started
                        this.dataReport.eventStart (this.reportSeq, "PlayState");
                        
                        //start quality timeInterval
                        this.setPlayQualityTimer ();
                } else if (this.peerConnection.iceConnectionState === "closed") {
                        this.dataReport.addEvent (this.reportSeq, "IceClosed");
                        
                        this.checkPlayConnectionFailedState (this.peerConnection.iceConnectionState);
                } else if (this.peerConnection.iceConnectionState === "failed") {
                        this.dataReport.addEvent (this.reportSeq, "IceFailed");
                        
                        this.checkPlayConnectionFailedState (this.peerConnection.iceConnectionState);
                }
        }
        
        private checkPlayConnectionFailedState (connectionState) {
                let state = null;
                if (connectionState == "failed") {
                        state = playErrorList.MEDIA_CONNECTION_FAILED;
                } else if (connectionState == "closed") {
                        state = playErrorList.MEDIA_CONNECTION_CLOSED;
                }
                
                if (state == null) {
                        return;
                }
                
                if (this.state != ENUM_PLAY_STATE.playing && this.retryState == ENUM_PLAY_STATE.didNotStart) {
                        this.logger.info ("zp.oics.1  state " + this.state + " retryState " + this.retryState + " connectionState " + connectionState);
                        
                        this.playStateUpdateError (state);
                }
                else {
                        if (this.shouldRetryPlay ()) {
                                this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.retry, this.streamId);
                                
                                this.startRetryPlay ();
                        }
                        else {
                                this.playStateUpdateError (state);
                        }
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // retry play
        /*
         *    "zp.srp.1.0": "ZegoPlayWeb.shouldRetryPlay"
         */
        private shouldRetryPlay () {
                if (this.retryState == ENUM_RETRY_STATE.didNotStart && this.state != ENUM_PLAY_STATE.playing) {
                        this.logger.info ("zp.srp.1.0 connection didn't success");
                        return false;
                }
                
                if (this.retryState == ENUM_RETRY_STATE.retrying) {
                        this.logger.info ("zp.srp.0.0 already retrying");
                        return false;
                }
                
                if (this.currentRetryCount > this.maxRetryCount) {
                        this.logger.info ("zp.srp.1.0 beyond max");
                        return false;
                }
                
                this.logger.debug ("zp.srp.1.0 call success");
                return true;
        }
        
        /*
         *    "zp.srp.1": "ZegoPlayWeb.startRetryPlay"
         */
        private startRetryPlay () {
                this.logger.debug ("zp.srp.0 call");
                
                var streamId = this.streamId;
                var remoteVideo = this.remoteVideo;
                var audioOutput = this.audioOutput;
                
                this.resetPlay ();
                
                this.tryStartPlay (streamId, remoteVideo, audioOutput);
        }
        
        
        private clearTryPlayTimer () {
                if (this.waitingServerTimer != null) {
                        clearTimeout (this.waitingServerTimer);
                        this.waitingServerTimer = null;
                }
        }
        
        /*
        *    "zp.tsp.1": "ZegoPublish.tryStartPlay"
        */
        private tryStartPlay (streamId, remoteVideo, audioOputput) {
                
                this.logger.debug ("zp.tsp.1 call");
                
                this.clearTryPlayTimer ();
                
                this.streamId = streamId;
                this.remoteVideo = remoteVideo;
                this.audioOutput = audioOputput;
                
                if (this.currentRetryCount > this.maxRetryCount) {
                        this.logger.error ("zp.tsp.1 beyond max limit");
                        //callback error
                        this.playStateUpdateError (playErrorList.WEBSOCKET_ERROR);
                        return;
                }
                
                this.retryState = ENUM_RETRY_STATE.retrying;
                this.currentRetryCount += 1;
                
                if (this.signal.isServerConnected ()) {
                        this.logger.debug ("zp.tsp.1 signal connected");
                        
                        this.startPlay (streamId, this.remoteVideo, this.audioOputput);
                }
                else {
                        //setTimer
                        this.logger.debug ("zp.tsp.1 signal server not connected");
                        
                        this.waitingServerTimer = setTimeout (() => {
                                this.tryStartPlay (streamId, this.remoteVideo, this.audioOputput);
                        }, this.waitingServerTimerInterval);
                }
        }
        
        private clearPlayQualityTimer () {
                if (this.qualityTimer != null) {
                        clearInterval (this.qualityTimer);
                        this.qualityTimer = null;
                }
                
                this.lastPlayStats = {
                        time: null,
                        audioBytesReceived: null,
                        videoBytesReceived: null,
                        framesDecoded: null,
                        framesDropped: null,
                        framesReceived: null
                };
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // reset function
        /*
         *    "zp.rp.1": "ZegoPlayWeb.resetPlay"
         */
        private resetPlay () {
                this.logger.info ("zp.rp.1 call");
                
                this.streamId = null;
                this.state = ENUM_PLAY_STATE.stop;
                
                if (this.peerConnection != undefined) {
                        this.peerConnection.close ();
                        this.peerConnection = null;
                }
                
                if (this.waitingOfferTimer != null) {
                        clearTimeout (this.waitingOfferTimer);
                        this.waitingOfferTimer = null;
                }
                
                if (this.waitICETimer != null) {
                        clearTimeout (this.waitICETimer);
                        this.waitICETimer = null;
                }
                
                this.clearPlayQualityTimer ();
                
                if (this.remoteVideo) {
                        this.remoteVideo.srcObject = null;
                        this.remoteVideo.oncanplay = null;
                        this.remoteVideo = null;
                }
                
                this.audioOputput = null;
                
                if (this.signal) {
                        this.signal.unregisterPushCallback ("MediaDescPush", this.sessionId);
                        this.signal.unregisterPushCallback ("CandidateInfoPush", this.sessionId);
                        this.signal.unregisterPushCallback ("CloseSessionPush", this.sessionId);
                        // this.signal.unregisterPushCallback('WebSocketDisconnect', this.sessionId);
                }
                
                // this.sessionId = 0;
                this.sessionSeq = 0;
                this.answerSeq = 0;
                
                
                this.videoSizeCallback = false;
                
                this.currentRetryCount = 0;
                this.retryState = ENUM_RETRY_STATE.didNotStart;
                this.clearTryPlayTimer ();
        }
        

        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // quality timer
        /*
         *    "zp.spq.1": "ZegoPlayWeb.setPlayQualityTimer"
         */
        private setPlayQualityTimer () {
                if (this.qualityTimer != null) {
                        return;
                }
                
                this.logger.debug ("zp.spq.1 startTimer");
                
                this.clearPlayQualityTimer ();
                
                this.qualityTimer = setInterval (() => {
                        
                        if (this.peerConnection) {
                                this.peerConnection.getStats (null).then ((results) => {
                                        this.getPlayStats (results);
                                }, (error) => {
                                        this.logger.info ("zp.spq.1 getStats error " + error.toString ());
                                });
                        }
                        
                }, this.qualityTimeInterval);
                
                this.lastPlayStats = {
                        time: 0,
                        audioBytesReceived: 0,
                        videoBytesReceived: 0,
                        framesDecoded: 0,
                        framesReceived: 0,
                        framesDropped: 0
                };
        }
        
        
        /*
         *    "zp.gps.1": "ZegoPlayWeb.getPlayStats"
         */
        private getPlayStats (results) {
                if (results == undefined) {
                        return;
                }
                
                var playData = {
                        audioFractionLost: 0,
                        audioPacketsLost:0,
                        videoBitrate: 0,
                        videoFPS: 0,
                        playData: 0,
                        nackCount: 0,
                        pliCount: 0,
                        sliCount: 0,
                        videoFractionLos: 0,
                        audioJitter: 0,
                        videoFractionLost: null,
                        videoFramesDecoded:0,
                        frameHeight: 0,
                        frameWidth: 0,
                        videoTransferFPS: 0,
                        videoFramesDropped: 0,
                        totalRoundTripTime: 0,
                        currentRoundTripTime: 0,
                        
                };
                var time = this.lastPlayStats.time;
                results.forEach ((result) => {
                        if ((result.type == "inbound-rtp" || (result.type == "ssrc" && result.bytesReceived != undefined)) && (result.mediaType == "audio" || result.id.indexOf ("AudioStream") >= 0)) {
                                //audio
                                if (time != 0) {
                                        playData['audioBitrate'] = 8 * (result.bytesReceived - this.lastPlayStats['audioBytesReceived']) / (result.timestamp - time);
                                }
                                
                                if (playData['audioBitrate'] < 0) {
                                        playData['audioBitrate'] = 0;
                                }
        
                                playData.audioJitter = result.jitter;
                                playData.audioPacketsLost=result.packetsLost;
                                playData.audioFractionLost = result.fractionLost;
                                
                                this.lastPlayStats.audioBytesReceived = result.bytesReceived;
                                this.lastPlayStats.time = result.timestamp;
                        }
                        else if ((result.type == "inbound-rtp" || (result.type == "ssrc" && result.bytesReceived != undefined)) && (result.mediaType == "video" || result.id.indexOf ("VideoStream") >= 0)) {
                                //video
                                if (time != 0) {
                                        playData.videoBitrate = 8 * (result.bytesReceived - this.lastPlayStats.videoBytesReceived) / (result.timestamp - time);
                                        playData.videoFPS = 1000 * (result.framesDecoded - this.lastPlayStats.framesDecoded) / (result.timestamp - time);
                                }
                                
                                if (playData.videoBitrate < 0) {
                                        playData.videoBitrate = 0;
                                }
                                
                                if (playData.videoFPS < 0) {
                                        playData.videoFPS = 0;
                                }
                                
                                //playData.jitter = result.jitter;
                                playData.nackCount = result.nackCount;
                                playData.pliCount = result.pliCount;
                                playData.sliCount = result.sliCount;
                                playData.videoFractionLost = result.fractionLost;
                                playData.videoFramesDecoded = result.framesDecoded
                                
                                this.lastPlayStats.videoBytesReceived = result.bytesReceived;
                                this.lastPlayStats.framesDecoded = result.framesDecoded;
                                this.lastPlayStats.time = result.timestamp;
                        }
                        else if (result.type == "track" && (result.kind == "video" || result.id.indexOf ("video") >= 0)) {
                                playData.frameHeight = result.frameHeight;
                                playData.frameWidth = result.frameWidth;
                                
                                if (time != 0) {
                                        playData.videoTransferFPS = 1000 * (result.framesReceived - this.lastPlayStats.framesReceived) / (result.timestamp - time);
                                        playData.videoFramesDropped = result.framesDropped - this.lastPlayStats.framesDropped;
                                }
                                
                                if (playData.videoTransferFPS < 0) {
                                        playData.videoTransferFPS = 0;
                                }
                                
                                if (playData.videoFramesDropped < 0) {
                                        playData.videoFramesDropped = 0;
                                }
                                
                                this.lastPlayStats.framesReceived = result.framesReceived;
                                this.lastPlayStats.framesDropped = result.framesDropped;
                        }
                        else if (result.type == "candidate-pair") {
                                if (result.totalRoundTripTime != undefined) {
                                        playData.totalRoundTripTime = result.totalRoundTripTime;
                                }
                                
                                if (result.currentRoundTripTime != undefined) {
                                        playData.currentRoundTripTime = result.currentRoundTripTime;
                                }
                        }
                });
                
                // this.logger.debug("zp.gps.1 audio: " + playData.audioBitrate + " video: " + playData.videoBitrate +
                // " FPS: " + playData.videoFPS + " transfer: " + playData.videoTransferFPS);
                
                this.uploadPlayQuality (playData);
                
                if (time != 0) {
                        this.onPlayQualityUpdate (this.streamId, playData);
                }
        }
        
        /*
         *    "zp.upq.1": "ZegoPlayWeb.uploadPlayQuality"
         */
        private uploadPlayQuality (playData) {
                if (!this.qualityUpload) {
                        return;
                }
                
                var timeStamp = Date.parse (new Date () + '');
                if (this.qualityUploadLastTime == 0 || timeStamp - this.qualityUploadLastTime >= this.qualityUploadInterval) {
                        this.logger.debug ("zp.upq.1 upload");
                        
                        playData["stream_type"] = "play";
                        playData["stream_id"] = this.streamId;
                        playData["timeStamp"] = timeStamp / 1000;
                        
                        this.signal.QualityReport (getSeq (), this.sessionId, playData, (seq, sessionId, data) => {
                                if (data.report !== undefined) {
                                        this.qualityUpload = data.report;
                                        this.qualityUploadInterval = data.report_interval_ms;
                                }
                        },   (err, seq)=> {
                                this.logger.info ("zp.upq.1 upload failed " + err);
                        });
                        
                        this.qualityUploadLastTime = timeStamp;
                }
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push resetSession
        /*
         *    "zp.orrs.1": "ZegoPlayWeb.onRecvResetSession"
         */
        private onRecvResetSession (seq: number, sessionId: number, data: any) {
                this.logger.info ("zp.orrs.1 received ");
                
                if (sessionId != this.sessionId) {
                        this.logger.info ("zp.orrs.1 cannot find session");
                        return;
                }
                
                this.dataReport.addEvent (this.reportSeq, "RecvResetSession");
                
                //check should retry
                if (this.shouldRetryPlay ()) {
                        this.startRetryPlay ();
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push closeSession
        /*
         *    "zp.orcs.1": "ZegoPlayWeb.onRecvCloseSession"
         */
        private onRecvCloseSession (seq: number, sessionId: number, data: any) {
                this.logger.info ("zp.orcs.1 reason: " + data.reason);
                
                this.dataReport.addEvent (this.reportSeq, "RecvCloseSession");
                
                this.signal.sendCloseSessionAck (seq, this.sessionId, 0);
        
                var error =  JSON.parse(JSON.stringify(playErrorList.SESSION_CLOSED));
                error.msg += data.reason;
                this.playStateUpdateError (error);
        }
       
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // onGotRemoteStream callback
        /*
         *    "zp.ogrs.1": "ZegoPlayWeb.onGotRemoteStream"
         */
        private onGotRemoteStream (event) {
                this.logger.info ("zp.ogrs.0 called " + event.stream);
                if (!this.remoteVideo) {
                        this.logger.error ("zp.ogrs.0 no remoteVideo");
                        return;
                }
                
                this.remoteVideo.srcObject = event.stream;
                
                if (this.audioOputput) {
                        this.setAudioDestination (this.audioOputput);
                }
                
                this.dataReport.addEvent (this.reportSeq, "GetRemoteStream");
        }
        
     
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // send candidate Info
        /*
         *    "zp.sci.1": "ZegoPlayWeb.sendCandidateInfo"
         */
        private sendCandidateInfo (candidateInfo) {
                this.logger.debug ("zp.sci.1 called");
                
                
                candidateInfo = candidateInfo.filter (function (item) {
                        if (item.candidate.indexOf ('tcp') > 0) {
                                return false;
                        }
                        return true;
                });
                
                if (!candidateInfo || candidateInfo.length < 1) {
                        this.logger.info ("zp.sci.1 cancelled");
                        return;
                }
                
                this.dataReport.eventStart (this.reportSeq, "SendIceCandidate");
                this.signal.sendCandidateInfo (getSeq (), this.sessionId, candidateInfo, (seq, sessionId, data) => {
                        this.logger.debug ("zp.sci.1 send success");
                        this.dataReport.eventEnd (this.reportSeq, "SendIceCandidate");
                },  (err, seq)=> {
                        this.logger.error ("zp.sci.1 failed to send: " + err.toString ());
                        this.dataReport.eventEndWithMsg (this.reportSeq, "SendIceCandidate", {
                                error: err
                        });
                        
                        this.playStateUpdateError (playErrorList.SEND_CANDIDATE_ERROR);
                });
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // error function
        private shouldSendCloseSession (errorCode) {
                if (this.state != ENUM_PLAY_STATE_UPDATE.stop && this.state != ENUM_PLAY_STATE.waitingSessionRsp) {
                        return true;
                }
                
                return false;
        }
        
        private playStateUpdateError (errorCode) {
                this.logger.debug("zp.psue.1 called " ,errorCode.code);
                if (this.sessionId != 0 && this.shouldSendCloseSession (errorCode)) {
                        this.signal.sendCloseSession (getSeq (), this.sessionId, 1);
                        this.closeSessionSignal = true;
                }
                
                this.state = ENUM_PLAY_STATE.stop;
                this.onPlayStateUpdate (ENUM_PLAY_STATE_UPDATE.error, this.streamId, errorCode);
                
                this.resetPlay ();
        }
        
        onPlayStateUpdate (type, streamId, error?) {
        };
        
        onPlayQualityUpdate (streamId, quality) {
        };
        
        onVideoSizeChanged (streamId, videoWidth, videoHeight) {
        };
        
        /*
         *    "zp.sp.1.1": "ZegoPlayWeb.stopPlay"
         */
        stopPlay () {
                
                this.logger.debug ("zp.sp.1.1 called");
                
                //send to server
                if (this.sessionId && !this.closeSessionSignal) {
                        this.signal.sendCloseSession(getSeq(), this.sessionId, 0);
                }
                
                this.dataReport.eventEndWithMsg (this.reportSeq, "PlayState", {
                        "state": this.state + ''
                });
                
                this.dataReport.addEvent (this.reportSeq, "StopPlay");
                
                this.dataReport.addMsgExt (this.reportSeq, {
                        "stream": this.streamId,
                        "sessionId": this.sessionId
                });
                
                this.dataReport.uploadReport (this.reportSeq, "RTCPlayStream");
                
                this.resetPlay ();
        };
        
        /*
         *    "zp.od.1": "ZegoPlayWeb.onDisconnect"
         */
        onDisconnect () {
                this.logger.info ("zp.od.1 call");
                
                // if (this.sessionId !== sessionId) {
                //     this.logger.info("zp.od.1 session is not same");
                //     return;
                // }
                
                this.logger.info ("zp.od.1 websocket disconnect");
                this.dataReport.addEvent (this.reportSeq, "OnDisconnect");
                
                this.playStateUpdateError (playErrorList.WEBSOCKET_ERROR);
        };
}
