import {ZegoDataReport} from "../common/zego.datareport";
import {ENUM_RETRY_STATE, ENUM_PUBLISH_STATE, VideoInfo} from "../common/zego.entity";
import {ZegoSignal} from "../common/zego.signal";
import {ENUM_PUBLISH_STATE_UPDATE, getSeq, publishErrorList} from "../common/zego.extern";
import * as adapter from "./adapter.js";
import {Logger} from "../common/zego.logger";

export class ZegoPublish {
        
        logger: Logger;
        signal: ZegoSignal;
        state = ENUM_PUBLISH_STATE.stop;
        
        sessionId = 0;
        
        // localVideo = null;
        // localStream = null;
        
        waitingICETimeInterval = 5000;
        waitingAnswerTimeInterval = 5000;
        candidateInfo = [];
        
        waitingICETimer = null;
        waitingAnswerTimer = null;
        
        qualityTimer = null;
        qualityTimeInterval: number;
        publishQualityList = [];
        maxQualityListCount = 10;
        lastPublishStats:any = {};
        
        reportSeq = getSeq ();
        dataReport: ZegoDataReport;
        
        
        //quality signal
        qualityUpload = false;
        qualityUploadInterval = 30 * 1000;
        qualityUploadLastTime = 0;
        qualitySeq = 0;
        
        //retry
        maxRetryCount = 3;
        currentRetryCount = 0;
        retryState = ENUM_RETRY_STATE.didNotStart;
        waitingServerTimerInterval = 3 * 1000;
        waitingServerTimer = null;
        
        videoInfo: VideoInfo = {
                width: 0,
                height: 0,
                frameRate: 0,
                bitRate: 0
        };
        
        
        offerSeq = 0;
        
        
        streamId: string;
        localStream: MediaStream;
        
        sessionSeq: number;
        
        peerConnection: RTCPeerConnection | any;
        qualityCount = 0;
        closeSessionSignal = false;
        
        
        constructor (log: Logger, signal: ZegoSignal, dataReport: ZegoDataReport, qualityTimeInterval: number) {
                this.logger = log;
                this.signal = signal;
                this.dataReport = dataReport;
                this.qualityTimeInterval = qualityTimeInterval;
                dataReport.newReport (this.reportSeq);
        }
        
        private publishStateUpdateError (errorCode: { code: string, msg: string }) {
                if (this.sessionId != 0 && this.shouldSendCloseSession (errorCode)) {
                        //send close session request
                        this.signal.sendCloseSession (getSeq (), this.sessionId, 1);
                        this.closeSessionSignal = true;
                }
                
                this.state = ENUM_PUBLISH_STATE.stop;
                this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.error, this.streamId, errorCode);
                
                this.resetPublish ();
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // reset
        /*
         *    "zp.rp.0": "ZegoPublish.resetPublish"
         */
        private resetPublish () {
                this.logger.info ("zp.rp.0 call");
                
                this.streamId = null;
                this.state = ENUM_PUBLISH_STATE.stop;
                
                if (this.peerConnection != undefined || this.peerConnection != null) {
                        this.peerConnection.close ();
                        this.peerConnection = null;
                }
                
                if (this.waitingAnswerTimer != null) {
                        clearTimeout (this.waitingAnswerTimer);
                        this.waitingAnswerTimer = null;
                }
                
                if (this.waitingICETimer != null) {
                        clearTimeout (this.waitingICETimer);
                        this.waitingICETimer = null;
                }
                
                this.clearPublishQualityTimer ();
                
                if (this.signal) {
                        this.signal.unregisterPushCallback ("CandidateInfoPush", this.sessionId);
                        this.signal.unregisterPushCallback ("MediaDescPush", this.sessionId);
                        this.signal.unregisterPushCallback ("CloseSessionPush", this.sessionId);
                        // this.signal.unregisterPushCallback('WebSocketDisconnect', this.sessionId);
                }
                
                // this.sessionId = 0;
                this.sessionSeq = 0;
                this.offerSeq = 0;
                this.candidateInfo = [];
                
                this.publishQualityList = [];
                
                this.qualityUploadLastTime = 0;
                
                this.currentRetryCount = 0;
                this.retryState = ENUM_RETRY_STATE.didNotStart;
                
                this.clearTryPublishTimer ();
        }
        
        private clearTryPublishTimer () {
                if (this.waitingServerTimer != null) {
                        clearTimeout (this.waitingServerTimer);
                        this.waitingServerTimer = null;
                }
        }
        
        
        private clearPublishQualityTimer () {
                if (this.qualityTimer != null) {
                        clearInterval (this.qualityTimer);
                        this.qualityTimer = null;
                }
                
                this.lastPublishStats = {};
                this.qualityCount = 0;
                
                //this.dataReport.uploadReport(this.qualitySeq, "RTCPublishQuality");
        }
        
        private shouldSendCloseSession (errorCode: { code: string, msg: string }) {
                if (this.state != ENUM_PUBLISH_STATE.stop && this.state != ENUM_PUBLISH_STATE.waitingSessionRsp) {
                        return true;
                }
                return false;
        }
        
        
        /*
       *    "zp.sp.0": "ZegoPublish.startPublish"
       */
        startPublish (streamId: string, localStream: MediaStream, videoInfo: VideoInfo) {
                this.logger.debug ("zp.sp.0 called");
                
                if (!streamId) {
                        this.logger.error ("zp.sp.0 streamId is null");
                        return;
                }
                
                this.streamId = streamId;
                this.localStream = localStream;
                if (videoInfo) {
                        this.videoInfo = videoInfo;
                }
                
                //send to server
                this.sessionSeq = getSeq ();
                this.dataReport.eventStart (this.reportSeq, "CreateSession");
                this.signal.createSession (this.sessionSeq, 0, streamId, (seq, sessionId, data) => {
                        this.dataReport.eventEndWithMsg (this.reportSeq, "CreateSession", {
                                sessionId: data.session_id
                        });
                        this.logger.info('zp.sp.0 sessionId:'+data.session_id);
                        if (this.sessionSeq != seq) {
                                this.logger.error ("zp.sp.0 seq is not match.");
                                return;
                        }
                        
                        if (data.result !== 0) {
                                this.logger.error ("zp.sp.0 create session failed " + data.result);
                                
                                this.publishStateUpdateError (publishErrorList.CREATE_SESSION_ERROR);
                        } else {
                                this.sessionId = data.session_id;
                                this.logger.debug ("zp.sp.0 create session success " + this.sessionId);
                                
                                this.onCreatePublishSessionSuccess (data);
                        }
                }, (err, seq) => {
                        this.dataReport.eventEndWithMsg (this.reportSeq, "CreateSession", {
                                error: err
                        });
                        
                        this.publishStateUpdateError (publishErrorList.SEND_SESSION_TIMEOUT);
                });
                
                this.state = ENUM_PUBLISH_STATE.waitingSessionRsp;
                this.logger.debug ("zp.sp.0 called success");
        };
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // create session result
        /*
         *    "zp.ops.0": "ZegoPublish.onCreatePublishSessionSuccess"
         */
        onCreatePublishSessionSuccess (data) {
                //create offer
                this.logger.debug ("zp.ops.0 called");
                // this.state = ENUM_PUBLISH_STATE.Start;
                
                let urls = [];
                if (data.turn_server) urls.push (data.turn_server);
                if (data.stun_server) urls.push (data.stun_server);
                
                let configuration = {
                        iceTransportPolicy : 'relay' as RTCIceTransportPolicy,
                        iceServers: [{
                                urls: urls,
                                username: data.turn_username,
                                credential: data.turn_auth_key
                        }]
                };
                
                this.logger.info ("zp.ops.0 username: " + data.turn_username);
                this.logger.info ("zp.ops.0 credential: " + data.turn_auth_key);
                
                this.peerConnection = new RTCPeerConnection (configuration);
                this.peerConnection.onicecandidate = (e) => {
                        this.onIceCandidate (e);
                };
                
                this.peerConnection.onsignalingstatechange = (e) => {
                        this.onConnectionStateChange (e);
                };
                
                this.peerConnection.oniceconnectionstatechange = (e) => {
                        this.onIceConnectionStateChange (e);
                };
        
                let videoTracks =[];
                let audioTracks =[];
                if (this.localStream) {
                        this.localStream.getTracks ().forEach (
                                    (track) => {
                                            this.peerConnection.addTrack (track, this.localStream);
                                    }
                        );
                        
                        let videoTracks = this.localStream.getVideoTracks ();
                        let audioTracks = this.localStream.getAudioTracks ();
                        if (videoTracks.length > 0) this.logger.info ("zp.ops.0 video device: " + videoTracks[0].label);
                        if (audioTracks.length > 0) this.logger.info ("zp.ops.0 audio device: " + audioTracks[0].label);
                        
                }
                
                let offerOptions = {
                        offerToReceiveAudio: audioTracks.length>0?1:0,
                        offerToReceiveVideo: videoTracks.length>0?1:0,
                };
        
                this.logger.info ("zp.ops.0 createOffer: " + offerOptions);
                //create offer
                this.dataReport.eventStart (this.reportSeq, "CreateOffer");
                this.peerConnection.createOffer (offerOptions).then (
                            (desc) => {
                                    this.dataReport.eventEnd (this.reportSeq, "CreateOffer");
                                    
                                    this.onCreateOfferSuccess (desc);
                            },
                            (error) => {
                                    this.dataReport.eventEndWithMsg (this.reportSeq, "CreateOffer", {
                                            error: error.toString ()
                                    });
                                    
                                    this.logger.error ("zp.ops.0 create offer error " + error.toString ());
                                    this.publishStateUpdateError (publishErrorList.CREATE_OFFER_ERROR);
                            }
                );
                
                //register callback
                this.signal.registerPushCallback ("CandidateInfoPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvCandidateInfo (seq, sessionId, data)
                });
                this.signal.registerPushCallback ("CloseSessionPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvCloseSession (seq, sessionId, data)
                });
                this.signal.registerPushCallback ("MediaDescPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvMediaDescription (seq, sessionId, data)
                });
                // this.signal.registerPushCallback("WebSocketDisconnect", this.sessionId, onDisconnect, this);
                this.signal.registerPushCallback ("SessionResetPush", this.sessionId, (seq: number, sessionId: number, data: any) => {
                        this.onRecvResetSession (seq, sessionId, data)
                });
                
                this.logger.debug ("zp.ops.0 call success");
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // create offer result
        /*
         *    "zp.oco.0": "ZegoPublish.onCreateOfferSuccess"
         */
        onCreateOfferSuccess (desc: { sdp: any }) {
                //this.logger.debug("zp.oco.0 success. before desc: " + desc.sdp);
                
                //change bandwidth
                if (this.videoInfo.bitRate != 0) desc.sdp = this.updateBandwidthRestriction (desc.sdp, this.videoInfo.bitRate);
                
                this.logger.debug ("zp.oco.0 localSdp " , desc.sdp);
                
                this.dataReport.eventStart (this.reportSeq, "SetLocalDescription");
                this.peerConnection.setLocalDescription (desc).then (
                            () => {
                                    this.dataReport.eventEnd (this.reportSeq, "SetLocalDescription");
                                    
                                    this.onSetLocalDescriptionSuccess (desc);
                            },
                            (error) => {
                                    this.dataReport.eventEndWithMsg (this.reportSeq, "SetLocalDescription", {
                                            error: error.toString ()
                                    });
                                    
                                    this.logger.error ("zp.oco.0 error " + error.toString ());
                                    this.publishStateUpdateError (publishErrorList.SET_LOCAL_DESC_ERROR);
                            }
                );
        }
        
        
        updateBandwidthRestriction (sdp, bandwidth) {
                let modifier = 'AS';
                if (adapter.browserDetails.browser === 'firefox') {
                        bandwidth = (bandwidth >>> 0) * 1000;
                        modifier = 'TIAS';
                }
                if (sdp.indexOf ('b=' + modifier + ':') === -1) {
                        // insert b= after c= line.
                        sdp = sdp.replace (/c=IN (.*)\r\n/,
                                    'c=IN $1\r\nb=' + modifier + ':' + bandwidth + '\r\n');
                }
                else {
                        sdp = sdp.replace (new RegExp ('b=' + modifier + ':.*\r\n'),
                                    'b=' + modifier + ':' + bandwidth + '\r\n');
                }
                
                return sdp;
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // setLocalDescription result
        /*
         *    "zp.osd.0": "ZegoPublish.onSetLocalDescriptionSuccess"
         */
        onSetLocalDescriptionSuccess (desc) {
                this.logger.debug ("zp.osd.0 success");
                
                //send offer to other peer
                let mediaDescription = {
                        sdp: desc.sdp,
                        width: this.videoInfo.width,
                        height: this.videoInfo.height,
                        frameRate: this.videoInfo.frameRate,
                        video_min_kpbs: this.videoInfo.bitRate,
                        video_max_kpbs: this.videoInfo.bitRate,
                        audio_kpbs: 48
                };
                
                this.offerSeq = getSeq ();
                this.dataReport.eventStart (this.reportSeq, "SendMediaDesc");
                this.signal.sendMediaDesc (this.offerSeq, this.sessionId, 0, mediaDescription, (seq, sessionId, data) => {
                        if (this.offerSeq != seq || this.sessionId != sessionId) {
                                this.logger.error ("zp.osd.0 seq or sessionId is not equal");
                                return;
                        }
                        
                        this.logger.debug ("zp.osd.0 send success");
                        this.dataReport.eventEnd (this.reportSeq, "SendMediaDesc");
                        
                        //set timer for waiting
                        this.waitingAnswerTimer = setTimeout (() => {
                                if (this.state == ENUM_PUBLISH_STATE.waitingServerAnswer) {
                                        this.logger.error ("zp.osd.0 waiting timeout");
                                        this.publishStateUpdateError (publishErrorList.SERVER_MEDIA_DESC_TIMEOUT);
                                }
                        }, this.waitingAnswerTimeInterval);
                        
                        this.state = ENUM_PUBLISH_STATE.waitingServerAnswer;
                        
                }, (err, seq) => {
                        this.dataReport.eventEndWithMsg (this.reportSeq, "SendMediaDesc", {
                                error: err
                        });
                        
                        this.publishStateUpdateError (publishErrorList.SEND_MEDIA_DESC_TIMEOUT);
                });
                
                this.state = ENUM_PUBLISH_STATE.waitingOffserRsp;
                this.logger.debug ("zp.osd.0 call success");
        }
        
        /*
         *    "zp.sp.0.1": "ZegoPublish.stopPublish"
         */
        stopPublish () {
                this.logger.debug ("zp.sp.0.1 called");
                
                //close session
                if (this.sessionId && !this.closeSessionSignal) {
                        this.signal.sendCloseSession(getSeq(), this.sessionId, 0);
                }
                
                this.dataReport.eventEndWithMsg (this.reportSeq, "PublishState", {
                        "state": this.state + ''
                });
                
                this.dataReport.addEvent (this.reportSeq, "StopPublish");
                
                this.dataReport.addMsgExt (this.reportSeq, {
                        "stream": this.streamId,
                        "sessionId": this.sessionId
                });
                
                this.dataReport.uploadReport (this.reportSeq, "RTCPublishStream");
                
                this.resetPublish ();
        };
        
        
        onPublishStateUpdate (type, streamId, error?) {
        };
        
        onPublishQualityUpdate (streamId, quality) {
        };
        
        /*
         *    "zp.od.0": "ZegoPublish.onDisconnect"
         */
        onDisconnect () {
                this.logger.info ("zp.od.0 call");
                
                // if (this.sessionId !== sessionId) {
                //     this.logger.info("zp.od.0 session is not same");
                //     return;
                // }
                
                this.logger.info ("zp.od.0 websocket disconnect");
                this.dataReport.addEvent (this.reportSeq, "OnDisconnect");
                
                this.publishStateUpdateError (publishErrorList.WEBSOCKET_ERROR);
        };
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // send candidate Info
        /*
         *    "zp.sci.0": "ZegoPublish.sendCandidateInfo"
         */
        sendCandidateInfo (candidateInfo: any[]) {
                this.logger.debug ("zp.sci.0 called");
                candidateInfo = candidateInfo.filter (function (item) {
                        if (item.candidate.indexOf ('relay') > 0) {
                                return true;
                        }
                        return false;
                });
                
                if (!candidateInfo || candidateInfo.length < 1) {
                        this.logger.info ("zp.sci.0 cancelled");
                        return;
                }
                
                this.dataReport.eventStart (this.reportSeq, "SendIceCandidate");
                this.signal.sendCandidateInfo (getSeq (), this.sessionId, candidateInfo, (seq, sessionId, data) => {
                        this.logger.debug ("zp.sci.0 send success");
                        this.dataReport.eventEnd (this.reportSeq, "SendIceCandidate");
                }, (err, seq) => {
                        this.logger.error ("zp.sci.0 failed to send: " + err.toString ());
                        
                        this.dataReport.eventEndWithMsg (this.reportSeq, "SendIceCandidate", {
                                error: err
                        });
                        
                        this.publishStateUpdateError (publishErrorList.SEND_CANDIDATE_TIMEOUT);
                });
        }
        
        
        /*
        *    "zp.oics.0": "ZegoPublish.onIceConnectionStateChange"
        */
        onIceConnectionStateChange (event: any) {
                
                if (this.state == ENUM_PUBLISH_STATE.stop || this.peerConnection == null) {
                        return;
                }
                
                this.logger.info ("zp.oics.0 stateChanged " + this.peerConnection.iceConnectionState);
                
                if (this.peerConnection.iceConnectionState === "connected") {
                        
                        this.logger.info ("zp.oics.0 connected state " + this.state);
                        
                        this.dataReport.eventEnd (this.reportSeq, "IceConnected");
                        
                        if (this.state != ENUM_PUBLISH_STATE.publishing) {
                                this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.start, this.streamId);
                        }
                        
                        this.state = ENUM_PUBLISH_STATE.publishing;
                        if (this.retryState != ENUM_RETRY_STATE.didNotStart) {
                                this.retryState = ENUM_RETRY_STATE.finished;
                                this.currentRetryCount = 0;
                        }
                        
                        //publish started
                        this.dataReport.eventStart (this.reportSeq, "PublishState");
                        
                        //start quality timeInterval
                        this.setPublishQualityTimer ();
                }
                else if (this.peerConnection.iceConnectionState === "closed") {
                        
                        this.dataReport.addEvent (this.reportSeq, "IceClosed");
                        
                        this.checkPublishConnectionFailedState (this.peerConnection.iceConnectionState);
                        
                }
                else if (this.peerConnection.iceConnectionState === "failed") {
                        this.dataReport.addEvent (this.reportSeq, "IceFailed");
                        
                        this.checkPublishConnectionFailedState (this.peerConnection.iceConnectionState);
                }
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // onIceCandidate callback
        /*
         *    "zp.oic.0": "ZegoPublish.onIceCandidate"
         */
        onIceCandidate (event: any) {
                
                if (!event.candidate) {
                        return;
                }
                
                this.logger.info ("zp.oic.0 candidate" + event.candidate.candidate);
        
                if (this.state < ENUM_PUBLISH_STATE.waitingServerICE || this.state == ENUM_PUBLISH_STATE.stop) {
                        //save candidate Info
                        
                        this.candidateInfo.push ({
                                candidate: event.candidate.candidate,
                                sdpMid: event.candidate.sdpMid,
                                sdpMLineIndex: event.candidate.sdpMLineIndex
                        });
                } else {
                        let candidate = {
                                candidate: event.candidate.candidate,
                                sdpMid: event.candidate.sdpMid,
                                sdpMLineIndex: event.candidate.sdpMLineIndex
                        };
                        
                        this.sendCandidateInfo ([candidate]);
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // onIceStateChange callback
        /*
         *    "zp.ocs.0": "ZegoPublish.onConnectionStateChange"
         */
        onConnectionStateChange (event) {
                this.logger.info ("zp.ocs.0 called "+event.target.signalingState);
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push SDP (setRemoteDesription)
        /*
         *    "zp.ormd.0": "ZegoPublish.onRecvMediaDescription"
         */
        onRecvMediaDescription (seq: number, sessionId: number, data: any) {
                this.logger.debug ("zp.ormd.0 received");
                if (this.state != ENUM_PUBLISH_STATE.waitingServerAnswer) {
                        this.logger.info ("zp.ormd.0 current state " + this.state + " not allowed");
                        return;
                }
                
                //clear timer
                if (this.waitingAnswerTimer != null) {
                        clearTimeout (this.waitingAnswerTimer);
                        this.waitingAnswerTimer = null;
                }
                
                this.dataReport.addEvent (this.reportSeq, "RecvMediaDesc");
                
                this.signal.sendMediaDescAck (seq, this.sessionId, 0);
                
                //not answer
                if (data.type == 1) {
                        this.onGetRemoteOfferSucceses (data.sdp);
                } else {
                        //server send error
                        this.publishStateUpdateError (publishErrorList.SERVER_MEDIA_DESC_ERROR);
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push offer (setRemoteDescription)
        /*
         *    "zp.oro.0": "ZegoPublish.onGetRemoteOfferSucceses"
         */
        onGetRemoteOfferSucceses (desc: RTCSdpType) {
                this.logger.debug ("zp.oro.0 remoteSdp:",desc);
                
                let answerDescription: RTCSessionDescription = {
                        type: "answer",
                        sdp: desc,
                        toJSON: () => {
                        }
                };
                
                this.dataReport.eventStart (this.reportSeq, "SetRemoteDescription");
                
                this.peerConnection.setRemoteDescription (new RTCSessionDescription (answerDescription)).then (() => {
                        this.logger.debug ("zp.oro.0 set success");
                        this.dataReport.eventEnd (this.reportSeq, "SetRemoteDescription");
                }, (error) => {
                        this.logger.error ("zp.oro.0 failed: " + error.toString ());
                        this.dataReport.eventEndWithMsg (this.reportSeq, "SetRemoteDescription", {
                                error: error.toString ()
                        });
                        
                        this.publishStateUpdateError (publishErrorList.SET_REMOTE_DESC_ERROR);
                });
                
                this.sendCandidateInfo (this.candidateInfo);
                this.candidateInfo = [];
                
                this.state = ENUM_PUBLISH_STATE.waitingServerICE;
                
                //setTimer
                this.waitingICETimer = setTimeout (() => {
                        if (this.state == ENUM_PUBLISH_STATE.waitingServerICE) {
                                this.logger.error ("zp.orod.0 waiting server timeout");
                                this.publishStateUpdateError (publishErrorList.SERVER_CANDIDATE_TIMEOUT);
                        }
                }, this.waitingICETimeInterval);
                
                this.logger.debug ("zp.oro.0 call success");
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push ICE (addICECandidate)
        /*
         *    "zp.oci.0": "ZegoPublish.onRecvCandidateInfo"
         */
        onRecvCandidateInfo (seq: number, sessionId: number, data: any) {
                this.logger.debug ("zp.oci.0 received " + data.infos.length);
                if (this.state != ENUM_PUBLISH_STATE.waitingServerICE) {
                        this.logger.info ("zp.oci.0 current state " + this.state + " not allowed");
                        return;
                }
                
                if (this.waitingICETimer != null) {
                        clearTimeout (this.waitingICETimer);
                        this.waitingICETimer = null;
                }
                
                this.dataReport.addEvent (this.reportSeq, "RecvIceCandidate");
                
                this.signal.sendCandidateInfoAck (seq, this.sessionId, 0);
                
                for (let i = 0; i < data.infos.length; i++) {
                        let ice = {
                                sdpMid: data.infos[i].sdpMid,
                                sdpMLineIndex: data.infos[i].sdpMLineIndex,
                                candidate: data.infos[i].candidate
                        };
                        
                        this.logger.debug ("zp.orci.0 candidate " + ice.candidate);
                        
                        this.peerConnection.addIceCandidate (new RTCIceCandidate (ice)).then (() => {
                                this.logger.debug ("zp.oci.0 add success");
                        }, (error) => {
                                this.logger.error ("zp.oci.0 add error " + error.toString ());
                                this.publishStateUpdateError (publishErrorList.SERVER_CANDIDATE_ERROR);
                        });
                }
                
                this.state = ENUM_PUBLISH_STATE.connecting;
                
                this.dataReport.eventStart (this.reportSeq, "IceConnected");
        }
        
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push closeSession
        /*
         *    "zp.orcs.0": "ZegoPublish.onRecvCloseSession"
         */
        onRecvCloseSession (seq: number, sessionId: number, data: any) {
                this.logger.info ("zp.orcs.0 reason: " + data.reason);
                
                this.dataReport.addEvent (this.reportSeq, "RecvCloseSession");
                
                this.signal.sendCloseSessionAck (seq, this.sessionId, 0);
                
                let error = JSON.parse(JSON.stringify(publishErrorList.SESSION_CLOSED));
                error.msg += data.reason;
                this.publishStateUpdateError (error);
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // server push resetSession
        /*
         *    "zp.orrs.0": "ZegoPublish.onRecvResetSession"
         */
        onRecvResetSession (seq: number, sessionId: number, data: any) {
                this.logger.info ("zp.orrs.0 received ");
                
                if (sessionId != this.sessionId) {
                        this.logger.error ("zp.orrs.0 cannot find session");
                        return;
                }
                
                this.dataReport.addEvent (this.reportSeq, "RecvResetSession");
                
                //check should retry
                if (this.shouldRetryPublish ()) {
                        this.startRetryPublish ();
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // retry publish
        /*
         *    "zp.srp.0.0": "ZegoPublish.shouldRetryPublish"
         */
        shouldRetryPublish () {
                if (this.retryState == ENUM_RETRY_STATE.didNotStart && this.state != ENUM_PUBLISH_STATE.publishing) {
                        this.logger.info ("zp.srp.0.0 connection didn't success");
                        return false;
                }
                
                if (this.retryState == ENUM_RETRY_STATE.retrying) {
                        this.logger.info ("zp.srp.0.0 already retrying");
                        return false;
                }
                
                if (this.currentRetryCount > this.maxRetryCount) {
                        this.logger.info ("zp.srp.0.0 beyond max");
                        return false;
                }
                
                this.logger.debug ("zp.srp.1.0 call success");
                return true;
        }
        
        /*
         *    "zp.srp.0": "ZegoPublish.startRetryPublish"
         */
        startRetryPublish () {
                this.logger.debug ("zp.srp.0 call");
                
                let streamId = this.streamId;
                if (!streamId) {
                        this.logger.info ("zp.srp.0 no streamid");
                        return;
                }
                
                this.resetPublish ();
                
                this.tryStartPublish (streamId);
        }
        
        /*
         *    "zp.tsp.0": "ZegoPublish.tryStartPublish"
         */
        tryStartPublish ( streamId) {
                
                this.logger.debug ("zp.tsp.0 call");
                
                this.clearTryPublishTimer ();
                
                this.streamId = streamId;
                if (this.currentRetryCount > this.maxRetryCount) {
                        this.logger.info ("zp.tsp.0 beyond max limit");
                        //callback error
                        this.publishStateUpdateError (publishErrorList.WEBSOCKET_ERROR);
                        return;
                }
                
                this.retryState = ENUM_RETRY_STATE.retrying;
                this.currentRetryCount += 1;
                
                if (this.signal.isServerConnected ()) {
                        this.logger.debug ("zp.tsp.0 signal connected");
                        
                        this.startPublish (streamId,this.localStream,this.videoInfo);
                }
                else {
                        //setTimer
                        this.logger.debug ("zp.tsp.0 signal server not connected");
                        
                        this.waitingAnswerTimer = setTimeout (() => {
                                this.tryStartPublish (streamId);
                        }, this.waitingAnswerTimeInterval);
                }
        }
        
        
        checkPublishConnectionFailedState (connectionState) {
                let state = null;
                if (connectionState == "failed") {
                        state = publishErrorList.MEDIA_CONNECTION_FAILED;
                }
                else if (connectionState == "closed") {
                        state = publishErrorList.MEDIA_CONNECTION_CLOSED;
                }
                
                if (state == null) {
                        return;
                }
                
                if (this.state != ENUM_PUBLISH_STATE.publishing && this.retryState == ENUM_PUBLISH_STATE.didNotStart) {
                        this.logger.info ("zp.oics.0  state " + this.state + " retryState " + this.retryState + " connectionState " + connectionState);
                        
                        this.publishStateUpdateError (state);
                }
                else {
                        if (this.shouldRetryPublish ()) {
                                this.onPublishStateUpdate (ENUM_PUBLISH_STATE_UPDATE.retry, this.streamId);
                                this.startRetryPublish ();
                        }
                        else {
                                this.publishStateUpdateError (state);
                        }
                }
        }
        
        //////////////////////////////////////////////////////////////////////////////////////////////
        // quality timer
        /*
         *    "zp.spq.0": "ZegoPublish.setPublishQualityTimer"
         */
        setPublishQualityTimer () {
                if (this.qualityTimer != null) {
                        return;
                }
                
                this.logger.debug ("zp.spq.0 called");
                
                this.clearPublishQualityTimer ();
                
                this.qualityTimer = setInterval (() => {
                        
                        if (this.peerConnection) {
                                this.peerConnection.getStats (null).then ((results) => {
                                        this.getPublishStats (results);
                                }, (error) => {
                                        this.logger.info ("zp.spq.0 getStats error " + error.toString ());
                                });
                        }
                        
                }, this.qualityTimeInterval);
                
                this.lastPublishStats = {
                        time: 0,
                        audioBytesSent: 0,
                        videoBytesSent: 0,
                        framesEncoded: 0,
                        framesSent: 0
                };
                
                this.qualitySeq = getSeq();
                this.qualityCount = 0;
                this.dataReport.newReport (this.qualitySeq);
        }
        
        /*
         *    "zp.gps.0": "ZegoPublish.getPublishStats"
         */
        getPublishStats (results) {
                if (!results) {
                        return;
                }
                
                let publishData = {
                        audioBitrate: 0,
                        videoBitrate: 0,
                        videoFPS: 0,
                        nackCount: 0,
                        pliCount: 0,
                        sliCount: 0,
                        frameHeight: 0,
                        frameWidth: 0,
                        videoTransferFPS: 0,
                        totalRoundTripTime: 0,
                        currentRoundTripTime: 0
                };
                let time = this.lastPublishStats.time;
                results.forEach ((result) => {
                        if ((result.type == "outbound-rtp" || (result.type == "ssrc" && result.bytesSent != undefined)) && result.mediaType == "audio") {
                                //audio
                                if (time != 0) {
                                        publishData.audioBitrate = 8 * (result.bytesSent - this.lastPublishStats.audioBytesSent) / (result.timestamp - time);
                                }
                                
                                if (publishData.audioBitrate < 0) {
                                        publishData.audioBitrate = 0;
                                }
                                
                                this.lastPublishStats.audioBytesSent = result.bytesSent;
                                this.lastPublishStats.time = result.timestamp;
                        }
                        else if ((result.type == "outbound-rtp" || (result.type == "ssrc" && result.bytesSent != undefined)) && result.mediaType == "video") {
                                //video
                                if (time != 0) {
                                        publishData.videoBitrate = 8 * (result.bytesSent - this.lastPublishStats.videoBytesSent) / (result.timestamp - time);
                                        publishData.videoFPS = 1000 * (result.framesEncoded - this.lastPublishStats.framesEncoded) / (result.timestamp - time);
                                }
                                
                                if (publishData.videoBitrate < 0) {
                                        publishData.videoBitrate = 0;
                                }
                                if (publishData.videoFPS < 0) {
                                        publishData.videoFPS = 0;
                                }
                                
                                publishData.nackCount = result.nackCount;
                                publishData.pliCount = result.pliCount;
                                publishData.sliCount = result.sliCount;
                                
                                this.lastPublishStats.videoBytesSent = result.bytesSent;
                                this.lastPublishStats.framesEncoded = result.framesEncoded;
                                this.lastPublishStats.time = result.timestamp;
                        }
                        //safari don't have this type
                        else if (result.type == "track" && (result.kind == "video" || result.id.indexOf ("video") >= 0)) {
                                publishData.frameHeight = result.frameHeight;
                                publishData.frameWidth = result.frameWidth;
                                
                                if (time != 0) {
                                        publishData.videoTransferFPS = 1000 * (result.framesSent - this.lastPublishStats.framesSent) / (result.timestamp - time);
                                }
                                
                                if (publishData.videoTransferFPS < 0) {
                                        publishData.videoTransferFPS = 0;
                                }
                                
                                this.lastPublishStats.framesSent = result.framesSent;
                        }
                        else if (result.type == "candidate-pair") {
                                if (result.totalRoundTripTime != undefined) {
                                        publishData.totalRoundTripTime = result.totalRoundTripTime;
                                }
                                
                                if (result.currentRoundTripTime != undefined) {
                                        publishData.currentRoundTripTime = result.currentRoundTripTime;
                                }
                        }
                });
                
                // this.logger.debug("zp.gps.0 audio: " + publishData.audioBitrate + " video: " + publishData.videoBitrate +
                //  " FPS: " + publishData.videoFPS + " transfer: " + publishData.videoTransferFPS);
                
                // this.dataReport.addEvent(this.qualitySeq, "PublishQuality", publishData);
                // this.qualityCount += 1;
                // if (this.qualityCount > this.maxQualityListCount) {
                //     this.dataReport.uploadReport(this.qualitySeq, "RTCPublishQuality");
                //     this.qualityCount = 0;
                //     this.qualitySeq = getSeq();
                //     this.dataReport.newReport(this.qualitySeq);
                // }
                
                //upload quality
                this.uploadPublishQuality (publishData);
                
                if (time != 0) {
                        this.onPublishQualityUpdate (this.streamId, publishData);
                }
        }
        
        
        /*
         *    "zp.upq.0": "ZegoPublish.uploadPublishQuality"
         */
        uploadPublishQuality (publishData) {
                if (!this.qualityUpload) {
                        return;
                }
                
                let timeStamp = Date.parse (new Date () + '');
                if (this.qualityUploadLastTime == 0 || timeStamp - this.qualityUploadLastTime >= this.qualityUploadInterval) {
                        this.logger.debug ("zp.upq.0 upload");
                        
                        publishData["stream_type"] = "publish";
                        publishData["stream_id"] = this.streamId;
                        publishData["timeStamp"] = timeStamp / 1000;
                        
                        this.signal.QualityReport (getSeq(), this.sessionId, publishData, (seq, sessionId, data) => {
                                if (data.report !== undefined) {
                                        this.qualityUpload = data.report;
                                        this.qualityUploadInterval = data.report_interval_ms;
                                }
                        }, (err, seq) => {
                                this.logger.info ("zp.upq.0 upload failed " + err);
                        });
                        
                        this.qualityUploadLastTime = timeStamp;
                }
        }
        
        
}
