import {Config} from "./zego.entity";
import {Logger} from "./zego.logger";

export class ClientUtil {
        
        static checkConfigParam (option: Config, logger: Logger): boolean {
                if (!option.appid) {
                        logger.error ("ccp.0 appid must be number");
                        return false;
                }
                
                if (!option.server) {
                        logger.error ("ccp.0 server must be string and not empty");
                        return false;
                }
                
                if (!option.idName) {
                        logger.error ("ccp.0 idName must be string and not empty");
                }
                
                return true;
                
        }
        
        
        static checkLoginParam (roomid: string, token: string) {
                return true;
        }
        
        
        static registerCallback (fName, option: { success?: Function, error?: Function }, callbackList: { [index: string]: Function }) {
                let sf, ef;
                if (option.success) sf = option.success;
                if (option.error) ef = option.error;
                callbackList[fName + "SuccessCallback"] = sf;
                callbackList[fName + "ErrorCallback"] = ef;
        }
        
        
        static actionErrorCallback (fName: string, callbackList: { [index: string]: Function }): Function {
                return callbackList[fName + "ErrorCallback"];
        }
        
        // 
        static actionSuccessCallback (fName, callbackList: { [index: string]: Function }): Function {
                return callbackList[fName + "SuccessCallback"];
        }
        
        
        /**
         
         */
        static getServerError (code) {
                const serverErrorList = {
                        1: "parse json error.",
                        1001: "login is processing.",
                        1002: "liveroom request error.",
                        1003: "zpush connect fail.",
                        1004: "zpush handshake fail.",
                        1005: "zpush login fail.",
                        1006: "user login state is wrong.",
                        1007: "got no zpush addr",
                        1008: "token error",
                        1009: "dispatch error",
                        2002: "biz channel error",
                        1000000000: "liveroom cmd error, result=",
                };
                
                if (code === 0) {
                        return {
                                code: "ZegoClient.Success",
                                msg: "success"
                        };
                }
                
                let err = {
                        code: "ZegoClient.Error.Server",
                        msg: "",
                };
                if (code > 1000000000) {
                        err.msg = serverErrorList[1000000000] + code;
                } else if (!serverErrorList[code]) {
                        err.msg = serverErrorList[code];
                } else {
                        err.msg = "unknown error code:" + code;
                }
                
                return err;
        }
        
        
        static isKeepTryLogin (code: number) {
                switch (code) {
                        case 1002: //liveroom connect error
                        case 1003: //zpush connect error
                                return true;
                        default:
                                return false;
                }
        }
        
        
        /*
        *    "msl.0": "ZegoClient.mergeStreamList",
        */
        static mergeStreamList (logger: Logger, idName: string, oldStreamList: any[], newStreamList: any[], callbackResult) {
                logger.debug ("msl.0 call");
                let addStreamList = [];
                let delStreamList = [];
                let updateStreamList = [];
                let flag;
                
                for (let i = 0; i < newStreamList.length; i++) {
                        if (newStreamList[i].anchor_id_name == idName) {
                                logger.debug ("msl.0 have self stream added");
                                continue;
                        }
                        flag = false;
                        for (let j = 0; j < oldStreamList.length; j++) {
                                if (newStreamList[i].stream_id === oldStreamList[j].stream_id) {
                                        if (newStreamList[i].extra_info !== oldStreamList[j].extra_info) {
                                                updateStreamList.push (newStreamList[i]);
                                        }
                                        flag = true;
                                        break;
                                }
                        }
                        if (!flag) {
                                addStreamList.push (newStreamList[i]);
                        }
                }
                
                for (let k = 0; k < oldStreamList.length; k++) {
                        flag = false;
                        for (let n = 0; n < newStreamList.length; n++) {
                                if (newStreamList[n].anchor_id_name == idName) {
                                        logger.debug ("msl.0 have self stream deleted");
                                        continue;
                                }
                                
                                if (oldStreamList[k].stream_id === newStreamList[n].stream_id) {
                                        flag = true;
                                        break;
                                }
                        }
                        if (!flag) {
                                delStreamList.push (oldStreamList[k]);
                        }
                }
                
                oldStreamList = newStreamList;
                callbackResult (addStreamList, delStreamList, updateStreamList);
                logger.debug ("msl.0 call success");
        }
        
        
        static checkCustomCommandParam (param: {
                dest_id_name: string[],
                custom_msg: string
        }) {
                return true;
        }
        
        //
        static generateRandumNumber (maxNum: number) {
                return parseInt (Math.random () * (maxNum + 1) + '', 10);
        }
        
        
        static isSupportWebrtc (): boolean {
                const e = window['RTCPeerConnection'] || window['mozRTCPeerConnection'] || window['webkitRTCPeerConnection'],
                            t = navigator['getUserMedia'] || navigator['webkitGetUserMedia'] ||
                                        navigator['msGetUserMedia'] || navigator['mozGetUserMedia'] ||
                                        navigator['mediaDevices'] && navigator['mediaDevices']['getUserMedia'],
                            n = window['WebSocket']
                return !!e && !!t && !!n;
        }
        
        static isSupportH264 (sucCall, errCall) {
                new RTCPeerConnection (null).createOffer ({
                        offerToReceiveAudio: 1,
                        offerToReceiveVideo: 1
                }).then (desc => {
                        if (desc && desc.sdp) {
                                const sdplist = desc.sdp.split ('\r\n');
                                const hasH264 = sdplist.some (item => {
                                        return item.startsWith ('a=rtpmap:') && (item.indexOf ('H264/') > -1)
                                });
                                sucCall (hasH264);
                        }
                }, err => {
                        errCall (err);
                });
        }
}
